.reals {
    display: none;
    padding-top: 80px;
    padding-left: 20px;
    overflow: hidden;
}

.reals__title {
    padding: 0;
    padding-left: 20px;
    margin: 0;
    margin-bottom: 40px;
    font-size: 30px;
    color: #000000;
    font-family: "TTFirsNeue-Bold";
    letter-spacing: 0.02em;
    line-height: 1.4;
}


.reals__slide {      
    width: 290px;
    height: 380px;
    position: relative;    
    margin-bottom: 40px; 
    padding-left: 10px;
    padding-right: 10px;

    &:hover {
        box-shadow: 0px 30px 30px -12px rgba(0, 0, 0, 0.05);
    }

    &:hover {
        box-shadow: 0px 30px 30px -12px rgba(0, 0, 0, 0.05);
    }
}

.reals video {        
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-color: transparent;
    border-radius: 18px;
}

.reals .video-controls {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 85%;
}

.reals .play-pause {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 85%;
    background-color: transparent;
    border: none;
    font-size: 0;
}

.reals video::-webkit-media-controls-play-button {
    border-radius: 8px;
    box-sizing: border-box;
    background-color: #C79B67;
    transform: scale(0.5, 0.5);
    padding: 0;
}


.reals video::-webkit-media-controls-fullscreen-button {
    display: none;
}

.reals__link {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: 10px;
    border: 1px solid #efefef;
    background-color: #ffffff;
    border-radius: 5px;
    color: #000000;
    border-radius: 12px;
    padding: 16px;
    font-family: "TTFirsNeue-medium";
    font-size: 14px;
    line-height: 14px;
    overflow: hidden;
    width: calc(100% - 30px);
    transition: all .3s linear;


    span {
      padding-right: 7px;
    }

    &:hover {      
        color: #BB1C26;
        transition: all .3s linear;
        border: 1px solid #D5D6DA;
    }

    &:active {
      color: #BB1C26;
      transition: all .3s linear;
      border: 1px solid #D5D6DA;
    }
}

@media (min-width: 576px) {
    .reals__title {
        padding-left: 33px;
        font-size: 36px;
    }
}    

@media (min-width: $tablet-width) {
    .reals__title {
        padding-left: 33px;
        font-size: 36px;
        margin-bottom: 60px;
    }

    .reals__link {
        position: absolute;
        top: -5px;
        right: 33px;
        width: 140px;
    }
}    

@media (min-width: 992px) {
    .reals {
        padding-left: 30px;
        padding-right: 30px; 
        margin: 0 auto;
    }

    .reals__title {
        padding-left: 0;
        font-size: 40px;
        margin-bottom: 70px;
    }
}    

@media (min-width: $desktop-width) {
    .reals {
        padding-top: 80px;
        padding-bottom: 50px;
        padding-left: 0;
        padding-right: 0;
        display: block;
        position: relative;
        background: none;

        &::before {
            //content: "";
            position: absolute;
            bottom: 0;
            right: -150px;
            left: -150px;
            height: 500px;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("../img/bg-form2-desktop.png");
          }
    }

    .reals__wrapper {
        position: relative;
        padding-top: 60px;
        max-width: 1390px;
        padding-left: 25px;
        padding-right: 25px;
        margin: 0 auto;

        &::before {
            content: "";
            position: absolute;
            right: 30px;
            top: 317px;
            height: 500px;
            width: calc(1340px + ((100vw - 1340px)/2));
            background-repeat: no-repeat;
            background-size: cover;
            background-position: right center;
            background-image: url("../img/bg-form2-desktop-left.svg");
            z-index: -1;
          }
    
          &::after {
            content: "";
            position: absolute;
            left: 30px;
            top: 317px;
            height: 500px;
            width: calc(1340px + ((100vw - 1340px)/2));
            background-repeat: no-repeat;
            background-size: cover;
            background-position: left center;
            background-image: url("../img/bg-form2-desktop-right.svg");
            z-index: -1;
          }
    }

    .reals__title {
        max-width: 630px;
        margin-bottom: 84px;
    }

    .reals__slide {
        padding-left: 20px;
        padding-right: 20px;
        height: 480px;
    }

    .reals__slider {
        margin: 0 -20px;
    }

    .reals__link {
        top: 88px;
        right: 19px;
    }
}    

@media (min-width: 1600px) { 
    .reals .slider .slick-next, .reals .slider .slick-prev {
      top: 46%;
    }  
  }    
  
  @media (min-width: 2500px) { 
    .reals__wrapper::before {    
      top: 325px;
    }  
  
    .reals__wrapper::after {
      top: 325px;
    } 
   
  }  

  @media (min-width: 2200px) { 
    .reviews::before {
        top: 260px;
    }   
  }    

  @media (min-width: 2500px) { 
    .reviews::before {
        top: 233px;
    }   
  }   



