
.slider .slick-dots {
    bottom: 12px;
    text-align: center;
    padding-left: 25px;
    box-sizing: border-box;

    li {
        background: #ffffff;
        width: 9px;
        height: 9px;
        border-radius: 30px;
        opacity: 1;
        -webkit-transition: .3s;
        -o-transition: .3s;
        transition: .3s;
    }

    li.slick-active {
        width: 40px;
        background: #BB1C26;
    }
}

.slider .slick-dots li.slick-active button:before {
    content: "";
    opacity: 0;
}

.slider .slick-dots li button:before {
    content: "";
    opacity: 0;
}    

@media (min-width: 576px) {
    .slider .slick-dots {
        bottom: 21px;
    }
}    

@media (min-width: $tablet-width) {
    .slider .slick-dots {
        bottom: 62px;
        text-align: left;
    }
}    

@media (min-width: 992px) {
    .slider .slick-dots {
        left: 57px;
        bottom: 70px;
    }
}   

@media (min-width: $desktop-width) {
    .slider .slick-dots {
        bottom: 86px;
    }
}    


.slider .slick-next, .slider .slick-prev {
    top: 45%;
    left: -88px;
    right: auto;
    width: 48px;
    height: 48px;
    transform: translateY(-50%);
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("../img/icon-prev-arrow.svg");
    z-index: 100;
}

.slider .slick-next {
    left: auto;
    right: -88px;
    background-image: url("../img/icon-next-arrow.svg");
}

.slider .slick-next:focus, .slider .slick-next:hover {
    background-image: url("../img/arrow-active-next.svg");
}

.slider .slick-prev:focus, .slider .slick-prev:hover {
    background-image: url("../img/arrow-active-prev.svg");
}

.slider .slick-next::before, .slider .slick-prev::before {
    content: "";
}
