
.nav__block-title {
    font-family: "TTFirsNeue-demibold";
    color: #000000;
    font-size: 16px;
}

.nav__add-submenu-item {
    margin-bottom: 12px;
}   

.nav__add-submenu-link {
    color: #585858;
    font-size: 15px;

    &:hover {
        color: #BB1C26;
    }

    &:active {
        color: #BB1C26;
    }
}

.nav__block-link {
    font-size: 15px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";

    &:hover {
        color: #BB1C26;
    }

    &:active {
        color: #BB1C26;
    }
}

.nav__link {
    font-size: 15px;
    font-family: "TTFirsNeue-demibold";
    color: #000000;

    &:hover {
        color: #BB1C26;
    }

    &:active {
        color: #BB1C26;
    }
}


@media (min-width: 768px) and (max-width: $mobile-width-only) {
    .nav__contacts-wrap {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }
}    

@media (min-width: 991px) {
    .nav {
        top: 150px;
    }

    .nav__accordeon-container {
        padding-top: 20px;
    }

    .nav__list {
        position: relative;
        margin-bottom: 60px;
    }

    .nav__contacts {
        display: none;
    }

    .nav__address {
        padding-top: 24px;
        padding-bottom: 24px;
        padding-left: 74px;
        width: 48%;

        &::before {
            top: 23px;
            left: 30px;
        }
        
    }

    .nav__email {
        padding-top: 24px;
        padding-bottom: 24px;
        padding-left: 74px;
        width: 48%;

        &::before {
            top: 26px;
            left: 30px;
        }
    }

    .nav {
        position: static;
        top: 0;
        height: auto;
        background-color: #F5F5F5;
    }

    .nav__block-link {
        padding-top: 22px;
        padding-bottom: 22px;
        display: block;
    }

    .nav__btn {
        display: none;
    }
    
    .nav__list {
        position: relative;
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;  
    }    

    .nav__item {
        padding-left: 10px;
        
        &:first-child {
            padding-left: 0;
        }
    }    

    .nav__link--active {
        display: block;
        position: relative;
        color: #E53935;        
    }         
    

    .nav__contacts-wrap {
        display: none;
    }
    /// !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    ///   top: 55px;
    .nav__submenu-section {
        position: relative;
        opacity: 0;
        margin: 0;
        padding-left: 40px;
        padding-right: 30px;
        list-style: none;
        position: absolute;        
        //top: 55px;
        top: 65px;
        left: 0;
        width: 0;
        overflow: hidden;
        max-height: 0; 
        background-color: #ffffff;
        z-index: 10;        
        box-shadow: 0px 20px 30px -12px rgba(34, 60, 80, 0.1);
        border-radius: 16px;
        background-color: #ffffff;
        transition: .2s ease-in;
        border: 1px solid #efefef;

        &::before {
            //content: "";
            position: absolute;
            height: 13px;
            top: 0;
            left: 0;
            right: 0;
            background-color: #f5f5f5;
        }
    }

    .nav__item--section { 
        padding-right: 20px;    
        position: static;     

            &::before {       
                top: 0;
                transform: translateY(50%) rotate(180deg);
                transition: .2s ease-in;
            }
        
    }   

    ///   HOVER

    .nav__item--section:hover {
        .nav__submenu-section {   
            padding-top: 40px;            
            opacity: 1;      
            width: calc(100vw - 70px);           
            max-height: 700px; 
            transition: .2s ease-in;
            padding-bottom: 40px;
        }
    }    
    
    
    .nav__item--section .nav__add-list {
        list-style: none;
        padding: 0;
        margin: 0;
        display: grid;
        grid-template-columns: repeat(4, auto);
        gap: 80px;
    }

    .nav__submenu-back {
        display: none;
    }

    .nav__accordeon-container {
        padding-top: 0;
    }
    

    .nav__submenu-item {
        margin-bottom: 8px;
    }

    .nav__submenu-link {
        color: #575757;
        font-size: 15px;
        cursor: pointer;

        &:hover {
            color: #E53935;
        }
    
        &:active {
            color: #E53935;
        }
    }

    .nav__item--mobile {
        display: none;
    }

    .nav__add-submenu {
        list-style: none;
        padding: 0;
        margin: 0;
    }

    .nav__menu-btn {
        display: none;
    }

    .nav__accordeon-container .nav__block-title {
        margin-bottom: 20px;
    }

    .nav__accordeon-container .nav__block-title {
        margin-bottom: 11px;
    }

    .nav__add-submenu-item {
        margin-bottom: 6px;
    }

    .nav__block-link {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            right: -24px;
            width: 16px;
            height: 16px;
            transform: translateY(-50%);
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("../img/icon-arrow-close.svg");
        }
    }

    .nav__block-link:hover {
        &::before {
            transform: translateY(-50%) rotate(180deg);
        }
    }

    .nav__section.active .nav__block-link {
        color:#BB1C26;

        &::before {
            transform: translateY(-50%) rotate(180deg);
        }
    }
    
}    


@media (min-width: $desktop-width) {
    .nav__item--section .nav__add-item {
        min-width: 247px;
     }

    .nav__item--section .nav__add-item {
        min-width: 247px;
    }

    .nav__item--section {
       
            .nav__submenu-section {
                max-width: 1340px;         
            }    
        }    
    
}    

@media (max-width: $mobile-width-only) {

    body.nav--opened {
        overflow-y: hidden;
    }
    
    .nav {
        opacity: 0;
        width: 0;
        height: 0;
        transition: all 0.5s ease-out;
        pointer-events: none;
    }

    body.nav--opened .nav { 
        opacity: 1;
        display: flex;
        position: absolute;
        top: 0;
        left: 0;
        height: calc(100vh);
        width: 100%;
        padding: 0;
        padding-top: 60px;
        padding-bottom: 0;
        overflow: hidden;
        overflow-y: auto;
        box-sizing: border-box;
        flex-direction: column;
        justify-content: flex-start;
        z-index: 1000;
        background-color: #F9F8F7;
        transition: all 0.5s ease-out;
        z-index: 10;
        border: 1px solid #efefef;
        pointer-events: auto;
    }

    body.nav--opened .nav.nav--submenu-opened { 
        overflow-y: hidden;
    }    

    .nav__wrap {
        width: 100%;
    }

    .nav__list {
        padding: 0;
        margin: 0;
        margin-bottom: 40px;
        list-style: none;
        border-top: 1px solid #efefef;
    }

    .nav__item {
        border-bottom: 1px solid #efefef;
    }
    

    .nav__link {
        display: block;
        padding-top: 19px;
        padding-bottom: 19px;
        cursor: pointer;
    }

    .nav__link--active {
        display: none;       
    }

    .nav__block-title {
        position: relative;   
        font-size: 15px;
        font-family: "TTFirsNeue-Bold";    
        color: #000000;
        height: 60px;
    }

    .nav__block-link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        padding-top: 17px;
        padding-bottom: 17px;
    }

    .nav__submenu-back {
        position: relative;
        background-color: #ffffff;
        color: #000000;
        font-size: 14px;
        padding: 16px 28px 16px 50px;
        border: 1px solid #efefef;
        border-radius: 12px;
        margin-bottom: 23px;

        &::before {
            content: "";
            position: absolute;
            top: 15px;
            left: 15px;
            width: 24px;
            height: 24px;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("../img/icon-arrow-back.svg");
        }
    }

    .nav__accordeon-container {
        position: relative;
        padding-top: 17px;
    }

    .nav__btn {
        display: block;
        background-color: transparent;
        border: 0;
        outline: 0;
        position: absolute;
        width: 100%;
        height: 58px;
        top: 0;
        left: 0;
        border: none;
    }
    
    .nav__btn-icon {
        position: absolute;        
        right: 5px;
        top: 10px;
        background-repeat: no-repeat;
        background-size: contain;
        width: 16px;
        height: 16px;
        background-color: #ffffff;
        background-image: url("../img/icon-arrow.svg");
        transform: translateY(50%) rotate(90deg);
        border: none;
        transition: .2s ease-in;
        background-color: transparent;
    }

    .nav__accordeon--closed .nav__btn-icon {
        top: 20px;        
        background-image: url("../img/icon-arrow.svg");
        transform: none;
        transform: translateY(50%) rotate(90deg);
        transition: .2s ease-in;
    }

    .nav__section {
        //position: relative;
        //padding-top: 17px;
    }
    
    .nav__submenu-section {       
        padding: 0;
        margin: 0;
        margin-left: 40px;
        list-style: none;
        max-height: 0;     
        opacity: 0;       
        will-change: max-height;
        //display: none;           
        transition: all 0.5s ease-out;
    }

    .nav__section--opened .nav__submenu-section {
        position: absolute;
        top:-60px;
        left: 0;
        width: 100%;
        opacity: 1;
        margin: 0;
        padding-top: 12px;
        list-style: none;
        box-sizing: content-box;
        height: 100vh;
        max-height: 100vh;
        overflow-x: hidden;
        overflow-y: auto;   
        scrollbar-color: dark;
        scrollbar-width: thin;
        overscroll-behavior: contain; 
        transition: all 0.5s ease-out;
        background-color: #F9F8F7;
        z-index: 1000;
        box-sizing: border-box;
    }

    .nav__submenu-block {
        padding-left: 20px;
        padding-right: 20px;
    }

    .nav__submenu {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    .nav__submenu-link {
        display: block;
        padding-top: 5px;
        padding-bottom: 5px;
        cursor: pointer;
        font-size: 15px;
        color: #575757;

        &:hover {
            color: #E53935;
        }

        &:active {
            color: #E53935;
        }
    } 

    .nav__add-list {
        margin: 0;
        padding: 0;
        list-style: none;
    }

    .nav__add-submenu {
        opacity: 1;
        margin: 0;
        padding: 0;
        padding-bottom: 19px;
        list-style: none;
        box-sizing: content-box;
        transition: all .2s ease-out;   
        max-height: auto;
        border-bottom: 1px solid #efefef;
        background-color: #F9F8F7;
    }    

    .nav__accordeon--closed .nav__add-submenu {
        margin: 0;
        padding: 0;
        list-style: none;
        max-height: 0;
        opacity: 0;
        transition: all .2s ease-out;
    }

    .nav__menu-btn {
        position: absolute;
        background-color: transparent;
        border: 0;
        display: block;
        width: 100%;
        height: 58px;
        top: 0;
        left: 0;
        border-top: 1px solid #efefef;
    }
    
    .nav__accordeon--closed .nav__menu-btn--last {
        border-bottom: 1px solid #efefef;
    }

    .nav__submenu-section .nav__accordeon-container .nav__block-title {
        height: 42px;
    }
    

    .nav__contacts-wrap {
        display: block;
    }

    .nav__tel {
        position: relative;
        display: block;
        color: #000000;
        font-size: 18px;
        font-family: "TTFirsNeue-demibold";
        padding-left: 40px;
        margin-bottom: 28px;

        &:hover {
            color: #BB1C26;
        }

        &:active {
            color: #BB1C26;
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-call.svg");
        }
    }

    .nav__email {
        display: block;
        color: #585858;
        position: relative;
        padding-left: 40px;
        margin-bottom: 35px;
        font-size: 14px;

        &:hover {
            color: #BB1C26;
        }

        &:active {
            color: #BB1C26;
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-email-bottom.svg");
        }

    }

    .nav__time {
        position: relative;
        padding-left: 40px;
        font-size: 14px;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-clock.svg");
        }

    }

    .nav__item--section {
        position: relative;
        cursor: pointer;       
        padding-right: 20px;
    
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            right: 4px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 16px;
            height: 16px;
            background-image: url("../img/icon-arrow.svg");
            transition: .2s ease-in;
        }
    }  
    
    /*  scrollbar  Chrome, Safari  Opera */
    body.nav--opened .nav::-webkit-scrollbar {
        display: none;
    }
    
    /*  scrollbar  IE, Edge  Firefox */
    body.nav--opened .nav {
        -ms-overflow-style: none;  /* IE  Edge */
        scrollbar-width: none;  /* Firefox */
    }

}  