.breadcrump {
    padding-top: 30px;
    padding-bottom: 30px;
}

.breadcrump__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: flex-start;

    li {
        position: relative;
        font-size: 13px;
        margin-right: 20px;

        &:last-child {
            margin-right: 0;

            &::before {
                content: "";
                width: 0;
                height: 0;
            }
        }

        &::before {
            content: "";
            position: absolute;
            top: 5px;
            right: -13px;
            width: 5px;
            height: 10px;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("../img/icon-page.svg");
        }
    }
}

.breadcrump-link {
    color: #585858;
    font-size: 13px;

    &:hover {
        color: #BB1C26;
    }

    &:active {
        color: #BB1C26;
    }
}

.breadcrump__list li:last-child {
    color: #000000;
}