.main-navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #efefef;
}

.main-navigation__logo {
    padding-top: 2px;
}

.main-navigation__address {
    display: none;
}

.main-navigation__toggle {
    display: none;
}

.main-navigation__callback {
    display: none;
}

.main-navigation__callback-tel {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 48px;
    height: 48px;
    background-color: #D32A34;
    border-radius: 12px;
    cursor: pointer;
    font-size: 0;
    transition: all 0.3s linear;

    &::before {
        content: "";
        display: block;
        background-repeat: no-repeat;
        background-size: contain;            
        width: 24px;
        height: 24px;
        background-image: url("../img/icon-tel.svg");
    }

    &:hover {
        background-color: #BB1C26;
    }

    &:active {
        background-color: #BB1C26;
    }
}

.main-navigation__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 48px;
    height: 48px;
    background-color: #B4864F;
    border-radius: 12px;
    cursor: pointer;
    font-size: 0;
    transition: all 0.3s linear;

    &::before {
        content: "";
        display: block;
        background-repeat: no-repeat;
        background-size: contain;            
        width: 24px;
        height: 24px;        
    }

    &:hover {
        background-color: #C79B67;
    }

    &:active {
        background-color: #C79B67;
    }
}

.main-navigation__btn--price {
    &::before {
        background-image: url("../img/icon-print.svg");
    }
}

.main-navigation__btn--email {
    &::before {
        background-image: url("../img/icon-email.svg");
        width: 20px;
        height: 16px;
    }
}

.main-navigation__logo {
    margin-bottom: 15px;
    padding-left: 20px;
    padding-right: 20px;
}
 

@media (min-width: 576px) {  
    .main-navigation {
        padding-top: 15px;
        padding-bottom: 15px;
    }

    .main-navigation__btn {
        width: 52px;
        height: 52px;
    }   
    
    .main-navigation__logo {
        margin-bottom: 0;
        padding-left: 10px;
        padding-right: 10px;
        flex-grow: 0;
    }

    .main-navigation__callback-tel {
        width: 52px;
        height: 52px;
    }

    .main-navigation__image {
        width: 200px;
        height: auto;
    }
    
}

@media (min-width: 768px) {

    .main-navigation__btn {
        width: 56px;
        height: 56px;
    }  

    

    .main-navigation {
        gap: 30px;
    }

    .main-navigation__logo {
        order: -1;
        flex-grow: 1;
        width: 210px;
        padding-left: 0;
        padding-right: 0;
        flex-grow: 1;
    }

    .main-navigation__image {
        width: 210px;
    } 
       

    .main-navigation__callback-tel {
        width: 58px;
        height: 58px;
        margin-left: 0;
    }    

}

@media (min-width: 992px) { 

    .main-navigation {
        padding-top: 35px;
        padding-bottom: 38px;
    }

    .main-navigation__logo {
        width: 230px;
    }

    .main-navigation__image {
        width: 230px;
    }

    .main-navigation__btn {
        position: relative;
        padding: 21px 27px 21px 54px;
        font-size: 15px;
        font-family: "TTFirsNeue-medium";
        color: #ffffff;
        width: auto;
        flex-shrink: 0;
        height: 60px;


        &::before {
            position: absolute;
            top: 50%;
            left: 20px;
            transform: translateY(-50%);
        }
    }

    .main-navigation__btn--email {
        display: none;
    }

    .main-navigation__callback-tel {
        position: relative;
        padding: 21px 25px 21px 56px;
        font-size: 15px;
        font-family: "TTFirsNeue-medium";
        color: #ffffff;
        width: auto;
        flex-shrink: 0;
        height: 60px;

        &::before {
            position: absolute;
            top: 50%;
            left: 20px;
            transform: translateY(-50%);
        }
    }
    
    .main-navigation__callback {
        display: block;
        text-align: right;
        padding-top: 7px;
        padding-bottom: 7px;
        margin-left: 7px;
    }

    .main-navigation__tel {
        display: block;
        position: relative;
        font-family: "TTFirsNeue-Bold";
        color: #000000;
        font-size: 18px;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: -28px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-tel.svg");
        }
    }

    .main-navigation__work {
        display: inline-block;
        position: relative;
        padding: 0;
        margin: 0;
        font-size: 14px;
    }
}    

@media (min-width: $desktop-width) {

    .main-navigation__callback {
        margin-left: 0;
        padding-bottom: 0;
        padding-bottom: 5px;
    }

    .main-navigation__price {
        width: 172px;
        height: 62px;
        margin-bottom: 5px;
        border-radius: 0;
        font-size: 14px;
        color: #ffffff;

        &::before {
            width: 0;
            height: 0;
        }
    }

    .main-navigation__logo {
        flex-grow: 0;
        margin-right: 20px;
    }  

    .main-navigation__address {
        position: relative;
        display: block;
        font-size: 15px;
        max-width: 258px;
        padding-left: 30px;
        margin-right: 20px;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: -8px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-map.svg");
        }
    }   

    
    .main-navigation__btn {
        margin-right: 5px;
    }    
   
}

@media (min-width: 1360px) { 

    .main-navigation__address {
        max-width: 320px;
    }   
}    

@media (max-width: $mobile-width-only) {

    body.nav--opened .main-navigation {
        padding-top: 20px;
        flex-direction: row-reverse;
    }

    body.nav--opened .main-navigation .main-navigation__toggle {
        width: 36px;
        height: 36px;
        border-radius: 50%;
        box-shadow: 0 17px 15px -12px rgba(130,104,74,.4);
        background-color: #C79B67;
    }

    body.nav--opened .main-navigation .main-navigation__toggle-inner {
        width: 13px;
        height: 14px;
        padding-top: 0;
    }

    body.nav--opened .main-navigation .main-navigation__toggle-inner::before {
        width: 13px;
    }

    body.nav--opened .main-navigation .main-navigation__toggle-inner::after {
        width: 13px;
    }

    body.nav--opened .main-navigation .main-navigation__btn {
        display: none;
    }

    body.nav--opened .main-navigation .main-navigation__logo {
        display: none;
    }

    body.nav--opened .main-navigation .main-navigation__callback-tel {
        display: none;
    }

    .main-navigation__toggle {
        position: relative;
        border: none;
        border-radius: 12px;
        width: 48px;
        height: 48px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #B4864F;
        transform: translateX(0);
        transition: transform 0.3s ease-in;
        color: #ffffff;
        z-index: 20;
      }

      .main-navigation__toggle-inner {
        padding-top: 2px;
        width: 18px;
        height: 17px;
      }
    
      /*.main-navigation__toggle:focus {
        outline: 0;
        color: #E53935;
      }*/
    
      .main-navigation__toggle-bar,
      .main-navigation__toggle-inner::after,
      .main-navigation__toggle-inner::before {
        content: "";
        display: block;
        width: 18px;
        height: 2px;
        margin-bottom: 4px;
        background-color: currentColor;
        transition: transform 0.3s ease;
      }
    
      .main-navigation__toggle-bar,
      .main-navigation__toggle-inner::after {
        margin-bottom: 0;
      }
    
      .main-navigation__toggle-bar {
        margin-bottom: 4px;
      }
    
      .main-navigation__bottom {
        padding-top: 0;
      }
    
      .main-navigation__toggle--active .main-navigation__toggle-bar {
        opacity: 0;
      }
    
      .main-navigation__toggle--active .main-navigation__toggle-inner::before {
        transform: translateY(6px) rotateZ(135deg);
      }
    
      .main-navigation__toggle--active .main-navigation__toggle-inner::after {
        transform: translateY(-6px) rotateZ(-135deg);
      }
    
}    


@media (max-width: 767px) and (min-width: 576px) {    
    .main-navigation__toggle {
        width: 52px;
        height: 52px;
    }       
}    

@media (max-width: 992px) and (min-width: 768px) {    
    .main-navigation__toggle {
        width: 58px;
        height: 58px;
    }       
}  


