.form-price {
    padding-top: 5px;
    padding-bottom: 20px;
}

.form-price__title  {
    padding: 0;
    margin: 0;
    margin-bottom: 26px;
    font-size: 30px;
    font-family: "TTFirsNeue-Bold";
    color: #000000;
    text-align: center;
    letter-spacing: 0.02em;
    line-height: 1.4;
}

.form-price__radio {
    display: none;
}

.form-price__section {
    position: relative;
}

.form-price__field.form-price__field--form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    margin-bottom: 28px;
}

.form-price__title-field {    
    padding: 0;
    margin: 0;
    grid-column: 1/3;
    font-size: 16px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";
    letter-spacing: 0.02em;
}

.form-price__field--choice .form-price__title-field {  
    margin-bottom: 19px;
}    

.form-price__field.form-price__field--choice {
    margin-bottom: 40px;
}

.form-price__section-radio {
    position: relative;
    height: 164px;

    &:last-child {
        height: 144px;
    }

    &:nth-last-child(-n+2) {
        height: 144px;
    }
}

.form-price__section {
    margin-bottom: 20px;
}

.form-price__radio+.form-price__label-radio {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    cursor: pointer;
    border: 1px solid #EFEFEF;
    border-radius: 12px;

    span {
        font-size: 14px;
        font-family: "TTFirsNeue-demibold";
        color: #000000;
    }
}    

.form-price__radio:checked+.form-price__label-radio::after {
    content: "";
    position: absolute;
    top: 50%;
    left: -10px;
    height: 32px;
    width: 32px;
    border-radius: 50%;
    //background-color: #D32A34;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../img/icon-tick-red.svg");
    transform: translateY(-50%);
}

.form-price__radio:checked+.form-price__label-radio {
    span {
        color: #BB1C26;
    }
}

.form-price__radio+.form-price__label-radio:hover {
    border: 1px solid #D5D6DA;

    span {
        color: #BB1C26;
    }
}

.form-price__img-wrap {
    position: relative;
    width: 64px;
    height: 64px;
    margin-bottom: 20px;
}

.form-price__image {
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
    position: absolute;
    top: 0;
    left: 0;
}

.form-price__label {
    display: none;
}


.form-price__text {
    padding-left: 30px;
    width: 100%;
    height: 68px;
    line-height: 66px;
    flex-grow: 1;
    font-size: 15px;
    border-radius: 12px;
    border: 1px solid #EFEFEF;
    color: #969696;
    outline:none;

    &:hover {
        color: #585858;
        border: 1px solid #D5D6DA;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.07) inset;
    }

    &:focus {
        color: #585858;
        border: 1px solid #D5D6DA;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.07) inset;
    }   
   

    &:active {
        color: #585858;
        border: 1px solid #D5D6DA;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.07) inset;
    }
}

.form-price__input--invalid {
    .form__text {
        outline: 1px solid #D32A34;
        color: #D32A34;
    }  
}

.form-price__input {
    position: relative;
}

.form-price__input input {
    margin-bottom: 20px;
}

.form-price__input .form-price__label {
    display: block;
    color: #969696;
    font-size: 15px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 30px; 
    top: 20px;
    opacity: 1;
    transition: 0.3s ease all;

    sup {
        color: #D32A34;
    }
}

.form-price__input input:hover ~ label {
    color: #585858;
}

.form-price__input input:focus ~ label,
.form-price__input input:valid ~ label {
    opacity: 0;
}

.form-price__field {
    margin-bottom: 20px;
}

.form-price__field--meta {
    padding: 20px;
    border-radius: 24px;
    border: 10px solid #EFEFEF;
}

.form-price__text-agree {    
    font-size: 13px;
    text-align: center;
    margin-bottom: 18px;

    span {
        color:#BB1C26;
    }
}

.form-price__send {
    width: 100%;
}


//SELECT

.form-price__select {
    position: relative;
    height: 68px;
    font-size: 15px;
    border: 1px solid #efefef;
    box-sizing: border-box;
    border-radius: 12px;
    color: #969696;
    width: 100%;
    -webkit-appearance: none;
    appearance: none;
    background-repeat: no-repeat;
    background-position: right;
    background-position-x: calc(100% - 28px);
}

.select {
    position: relative;
}

.select__head {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    max-width: 100%;
    padding-left: 30px;
    padding-right: 20px;
    cursor: pointer;
    overflow: hidden;
    overflow-y: auto;
    font-size: 15px;

    &::after {
        width: 16px;
        height: 16px;
        background-image: url("../img/icon-arrow-bottom.svg");
        background-repeat: no-repeat;
        background-position: center;
        position: absolute;
        right: 26px;
        bottom: 50%;
        transform: translateY(50%);
        content: "";
        display: block;
        transition: .2s ease-in;
    }
}

.select__head.open {
    &::after {
        transform: translateY(50%) rotate(180deg);
        border: none;
        transition: .2s ease-in;
    }
}

.select__list {
    position: relative;
    margin: 0;
    margin-top: -25px;
    display: block;
    position: absolute;
    top: 100%;
    left: -1px;
    right: 0;
    background-color: #ffffff;
    border-top: 1px solid #ffffff;
    border-left: 1px solid #EFEFEF;
    border-right: 1px solid #EFEFEF;
    border-bottom: 1px solid #EFEFEF;        
    padding: 20px 28px 20px 30px;    
    z-index: 10;
    border-radius: 0 0 12px 12px;
    box-shadow: 0px 30px 30px -12px rgba(0, 0, 0, 0.05);   
    
    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        height: 120px;
        overflow-x: hidden;
        overflow-y: auto;
        scrollbar-color: dark;
        scrollbar-width: thin;
        overscroll-behavior: contain;
    }
}


.select__item {    
    color: #969696;
    font-size: 14px;
    margin-bottom: 9px;

    &:hover {
        color: #BB1C26;
    }

    &:active {
        color: #BB1C26;
    }
}

//SCROLL

html .select__list ul {
    scrollbar-width: thin;
    scrollbar-color: #C79B67 #EFEFEF;
 }

.select__list ul::-webkit-scrollbar
{    
    background-color: #efefef;    
    width: 8px;
    overflow: hidden;
    border-radius: 2px;
}

.select__list ul::-webkit-scrollbar-thumb
{
    background-color: #C79B67;    
    border-radius: 2px;
}

.form-price .callback-opened .callback {   
    height: 101%;
}    

@media (min-width: 576px) {
    .form-price {
        padding-top: 20px;
        padding-bottom: 40px;
    }

    .form-price__title {
        font-size: 36px;
        margin-bottom: 50px;
    }

    .form-price__section {
        margin-bottom: 30px;
    }

    .form-price__field.form-price__field--choice {
        margin-bottom: 50px;
    }

    .form-price__field.form-price__field--form {
        grid-template-columns: repeat(3, 1fr);
        column-gap: 30px;
        margin-bottom: 20px;
    }

    .form-price__title-field {  
        grid-column: 1/4;
    }   

    .form-price__field--meta {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
        padding: 30px;
    }

    .form-price__text--name, .form-price__text--phone {
        margin-bottom: 0;
    }

    .form-price__text-agree {
        text-align: left;
        padding-top: 5px;
        margin-bottom: 0;
    }    

    .form-price__section-radio {
        margin-bottom: 10px;
    }

    .form-price__section-radio:nth-last-child(-n+3) {
        height: 144px;
    }

    .form-price__input input {
        margin-bottom: 0;
    }

    .form-price__radio:checked+.form-price__label-radio::after {
        left: -17px;
    }

}    


@media (min-width: 768px) {
    .form-price {
        padding-top: 30px;
        padding-bottom: 50px;
    }    

    .form-price__field--choice {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;
    }

    .form-price__field.form-price__field--form {
        margin-bottom: 23px;
    }    

    .form-price__field.form-price__field--choice {
        margin-bottom: 20px;
    }

    .form-price__field--meta {
        border: 15px solid #efefef;
        border-radius: 30px;
    }   

    .form-price__title {
        text-align: left;
    }

    .form-price__section-radio {
        height: 140px;
    }

    .form-price__radio:checked+.form-price__label-radio::after {
        left: -20px;
    }   

    .select__head span {
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical; 
    }   

    .select__head::after {
        right: 34px;
    }    

    .form-price__text-agree {
        padding-top: 14px;
        font-size: 14px;
    }

    .select__list {
        padding: 20px 35px 20px 30px;    
    }

}    

@media (min-width: 992px) {
    .form-price {
        padding-top: 50px;
        padding-bottom: 65px;
    }

    .form-price__form-wrap {
        border: 1px solid #efefef;
        border-radius: 30px;
        padding: 40px 40px 20px 40px;
        margin-bottom: 62px;
    }

    .form-price__title {
        font-size: 40px;
        margin-bottom: 70px;
    }

    .form-price__section-radio {
        height: 104px;
    }

    .form-price__section-radio:nth-last-child(-n+3) {
        height: 104px;
    }

    .form-price__field.form-price__field--form {
        margin-bottom: 28px;
    }

    .form-price__section {
        margin-bottom: 0;
    }

    .form-price__title-field {
        font-size: 18px;
        letter-spacing: 0;
        margin-bottom: 8px;
    }

    .form-price__radio+.form-price__label-radio {
        flex-direction: row;
        align-items: center;
        padding: 18px 30px;

        span {
            text-align: left;
            font-size: 15px;
        }
    }   

    .form-price__img-wrap {
        margin-bottom: 0;
        margin-right: 20px;
        width: 64px;
        height: 64px;
    }    
}    


@media (min-width: $desktop-width) {
    .form-price {
        padding-top: 100px;
        padding-bottom: 160px;
    }

    .form-price__title {
        margin-bottom: 90px;
    }

    .form-price__form-wrap {
        padding: 50px;
        padding-bottom: 32px;
    }

    .form-price__field.form-price__field--form {
        grid-template-columns: repeat(6, 1fr);
        gap: 20px;
        margin-bottom: 32px;
    }

    .form-price__title-field {  
        grid-column: 1/7;
        font-size: 20px;
    }   

    .form-price__field--meta {
        display: grid;
        grid-template-columns: repeat(4, 1fr);        
        padding: 38px;
    }

    .form-price__section-radio {
        height: 165px;
    }

    .form-price__section-radio:nth-last-child(-n+3) {
        height: 165px;
    }

    .form-price__radio+.form-price__label-radio {
        flex-direction: column;
        padding: 25px 9px;
        align-items: center;
    }    

    .form-price__radio:checked+.form-price__label-radio::after {
        top: -15px;
        left: 50%;
        transform: translateX(-50%);
    }   

    .form-price__img-wrap {
        margin-bottom: 20px;
        margin-right: 0;
    }
}  

@media (min-width: 1260px) {
    .form-price__field--meta {
        padding: 40px;
        gap: 40px;
    }

    .form__main {
        gap: 40px;
    }
}    
