.card {
   position: relative;
   border: 1px solid #efefef;
   border-radius: 18px;
   cursor: pointer;
   background-color: #ffffff;

  &:hover {
    box-shadow: 0px 30px 30px -12px #0000000D;
  }
}    

.card__slide {
    position: relative;
    height: 200px;
}

.card__image-wrap {
    position: relative;
    height: 200px;
}

.card__slide .slick-dots {
    opacity: 0;
}

.card__image {
    border-radius: 18px  18px 0 0;
    width: 100%;
    position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
}

.card__title {
    padding: 0;
    margin: 0;
    margin-bottom: 15px;
    font-family: "TTFirsNeue-demibold";
    font-size: 16px;
    color: #000000;
    max-height: 53px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical; 

    &:hover {
        color: #BB1C26;
    }

    &:active {
        color: #BB1C26;
    }
}

.card__info {
    padding-top: 22px;
    padding-right: 29px;
    padding-left: 30px;
    padding-bottom: 40px;
}

.card__text {
    padding: 0;
    margin: 0;
    font-size: 14px;
    font-family: "TTFirsNeue-medium";
}

.card__price {
    font-family: "TTFirsNeue-Bold";
    font-size: 22px;
    color: #C79B67;
    margin-bottom: 25px;
}

.card__link {
    text-align: center;
    background-color: #D32A34;    
    border: 0;
    border-radius: 12px;
    padding: 14px 20px;
    font-size: 13px;
    line-height: 13px;
    overflow: hidden;
    z-index: 1;
    color: #fff;
    transition: all .3s linear;

    &:hover {
        background-color: #BB1C26;
        transition: all .3s linear;
    }

    &:active {
        background-color: #BB1C26;
        transition: all .3s linear;
    }
}

.card__sale {
    position: absolute;
    top: 26px;
    left: 33px;
    padding: 9px 11px;
    font-size: 10px;
    line-height: 10px;
    font-family: "TTFirsNeue-demibold";
    color: #ffffff;
    letter-spacing: 0.02em;
    background-color: #D32A34;
    border-radius: 6px;
    text-transform: uppercase;
}

.card__promo {
    position: absolute;
    top: 26px;
    left: 33px;
    padding: 10px;
    font-size: 10px;
    line-height: 10px;
    font-family: "TTFirsNeue-demibold";
    color: #ffffff;
    background-color: #000000;
    border-radius: 6px;
    text-transform: uppercase;
}

.card__slider.slick-dotted.slick-slider {
    margin-bottom: 0;
}

.card__slider .slick-dots { 
    bottom: 20px;
}    

.card .slick-slide {
    position: relative;
}

.card__count {
    display: none;
}

@media (min-width: $tablet-width) {
    .card__slider .slick-dots {
        bottom: 23px;
    }

    .card__title {
        font-size: 18px;
    }

}   

@media (min-width: 992px) {    
    .card .slider .slick-dots {
        left: -2px;
        bottom: 22px;
    }

    .card__count {
        display: block;
        position: absolute;
        top: 30px;
        left: 30px;
        padding: 8px 12px;
        color: #ffffff;
        background-color: #000000;
        border-radius: 6px;
        font-size: 13px;
        line-height: 13px;
        font-family: "TTFirsNeue-medium";
        letter-spacing: 0.02em;
    }

    .card__count-slides {
        color: #7F7F7F;
    }
}    

@media (min-width: $desktop-width) {
    .card__image-wrap, .card__slide {
        height: 280px;
    }

    .card__info {
        position: relative;
        padding: 30px 40px 40px 40px;
    }

    .card__link {
        position: absolute;
        right: 37px;
        bottom: 40px;
        padding: 21px 24px;
        font-size: 14px;
    }

    .card__price {
        font-size: 26px;
        margin-bottom: 0;
    }

    .card__text {
        font-size: 15px;
    }

    .card__title {
        margin-bottom: 40px;
    }

    .card .slider .slick-dots {
        text-align: left;
        padding-left: 40px;
    }

    .card__count {
        top: 30px;
        left: 40px;
    }

}    