.menu-add {
    display: none;
}

.menu__bottom {
    display: block;
    //pointer-events: none;
}

.menu__top {
    display: none;
}

.menu__main {
    background-color: #ffffff;
}


@media (min-width: 992px) { 
    .menu__bottom {
        display: block;
        pointer-events: auto;
    }

    .menu__top {
        padding-top: 20px;
        padding-bottom: 18px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #efefef;
    }
    
    .menu__top-list {
        padding: 0;
        margin: 0;
        list-style: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .menu__top-item {
        margin-right: 60px;
        &:last-child {
            margin-right: 0;
        }
    }

    .menu__top-link {
        color: #585858;        
        font-size: 14px;


        &:hover {
            color: #BB1C26;
        }

        &:active {
            color: #BB1C26;
        }
    }

    .menu__email {
        position: relative;
        color: #585858;
        font-size: 14px;

        &:hover {
            color: #BB1C26;
        }

        &:active {
            color: #BB1C26;
        }

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: -40px;
            background-repeat: no-repeat;
            background-size: contain;            
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-email-top.svg");
            transform: translateY(-50%);
        }
    }
}    

