.notice {
    padding-top: 40px;
    padding-bottom: 40px;
}

.notice .slick-slider {
    margin-bottom: 0;
}

.notice__slide {
    position: relative;
    width: 100%;
    height: 380px;
    overflow: hidden;
    border-radius: 20px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}    

.notice__text-wrap {
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    padding: 15px;
    border-radius: 12px;
    background-color: rgba(255, 255, 255, 0.6);
    text-align: center;
    z-index: 100;
}

.notice__title {
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    font-size: 16px;
    font-family: "TTFirsNeue-Bold";
    color: #000000;
    line-height: 1.4;
    letter-spacing: 0.02em;
}

.notice__text {
    padding: 0;
    margin: 0;
    font-size: 14px;
}

.notice__list {
    display: none;
}

.notice__list-add {    
    margin: 0;
    padding: 16px 20px;
    font-size: 14px;
    letter-spacing: 0.02em;
    list-style: none;
    border: 1px solid #efefef;
    border-radius: 20px;
}

.notice__item-add {
    position: relative;
    padding: 10px 10px 10px 54px;
    margin-bottom: 13px;
    border-radius: 12px;       
    background-color: rgba(255, 255, 255, 0.6);

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 3px;
        height: 35px;
        width: 34px;
        transform: translateY(-50%);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
    }

    &:last-child {
        margin-bottom: 0;
    }
}


.notice__item-add--production {
    &::before {
        background-image: url("../img/icon-courthouse.svg");
    }
}

.notice__item-add--quality {
    &::before {
        background-image: url("../img/icon-tick.svg");
    }
}

.notice__item-add--individually {
    &::before {
        background-image: url("../img/icon-lamp.svg");
    }
}

.notice__image {
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: right center;
    position: absolute;
    top: 0;
    left: 0;
}

@media (min-width: 576px) {
    .notice {
        padding-top: 50px;
        padding-bottom: 50px;
    }

    .notice__image {
        //object-position: right top;
    }

    .notice__text-wrap {
        top: 30px;
        left: 30px;
        right: 30px;
        padding-top: 20px;
        padding-bottom: 20px;
    }

    .notice__title {
        font-size: 20px;
    }

    .notice__text {
        font-size: 16px;
    }

    .notice__list-add {    
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        padding-bottom: 25px;
    }

    .notice__item-add {
        padding: 62px 15px 0 15px;
        margin-bottom: 0;
        text-align: center;

        &::before {
            top: 13px;
            left: 50%;
            transform: translateX(-50%);
        }
    }
    
}    


@media (min-width: $tablet-width) {
    .notice {
        padding-top: 50px;
        padding-bottom: 35px;
    }

    .notice .slick-slider {
        margin-bottom: 20px;
    }


    .notice__text-wrap {
        top: 78px;
        right: 120px;
        text-align: left;
        padding: 0;
        background-color: transparent;
    }

    .notice__image {
        object-position: 50% top;
    }    

    .notice__title {
        font-size: 24px;
        margin-bottom: 15px;
    }

    .notice__text {
        margin-bottom: 30px;
    }

    .notice__list {    
        display: flex;
        justify-content: space-between;
        margin: 0;
        padding: 0;
        font-size: 14px;
        list-style: none;
    }
    
    .notice__item {
        position: relative;
        padding: 10px 10px 10px 54px;
        border-radius: 12px;       
        background-color: rgba(255, 255, 255, 0.6);
        margin-right: 24px;
        line-height: 1.4;
    
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 10px;
            height: 35px;
            width: 34px;
            transform: translateY(-50%);
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
        }
    }
    
    
    .notice__item--production {
        &::before {
            background-image: url("../img/icon-courthouse.svg");
        }
    }
    
    .notice__item--quality {
        &::before {
            background-image: url("../img/icon-tick.svg");
        }
    }
    
    .notice__item--individually {
        &::before {
            background-image: url("../img/icon-lamp.svg");
        }
    }

    .notice__list-add {
        display: none;
    }
    
}

@media (min-width: 992px) {
    .notice {
        padding-top: 60px;
        padding-bottom: 40px;
    }

    .notice__text {
        font-size: 18px;
        margin-bottom: 40px;
    }

    .notice__slide {
        height: 460px;  
    }  

    .notice__list {
        padding-right: 30px;
    }

    .notice__item {
        padding: 20px 30px 20px 74px;
        margin-right: 20px;
        font-size: 15px;
        line-height: 1.4;

        &::before {
            left: 21px;
        }
    }

    .notice__text-wrap {
        top: 78px;
        left: 60px;
        right: 174px;
    }

    .notice__title {
        font-size: 30px;
    }
}    


@media (min-width: $desktop-width) {
    .notice__slide {
        height: 505px;  
    }    
    

    .notice__title {
        font-size: 34px;
        margin-bottom: 17px;
    }

    .notice__text-wrap {
        top: 95px;
        left: 70px;
        right: auto;
        width: 695px;
    }
}    