.advantages {
    padding-top: 40px;
    padding-bottom: 30px;
    background: linear-gradient(180deg, #F9F8F7 20.9%, rgba(255, 255, 255, 0) 108%);
}

.advantages__title {
    padding: 0;
    margin: 0;
    margin-bottom: 40px;
    font-size: 30px;
    color: #000000;
    font-family: "TTFirsNeue-Bold";
    line-height: 1.4;
    text-align: center;
    letter-spacing: 0.02em;
}


.advantages__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.advantages__item {
    position: relative;
    border: 1px solid #efefef;
    border-radius: 16px;
    background-color: #ffffff;
    padding: 104px 19px 20px 20px;
    font-family: "TTFirsNeue-medium";
    color: #000000;
    font-size: 14px;
    line-height: 1.4;

    &::before {
        content: "";
        position: absolute;
        top: 20px;
        left: 20px;
        width: 62px;
        height: 62px;
        background-repeat: no-repeat;
        background-size: contain;
    }
}

.advantages__item--fitting {
    &::before {
        background-image: url("../img/icon-fitting.svg");
    }   
}

.advantages__item--design {
    &::before {
        background-image: url("../img/icon-design2.svg");
    }    
}

.advantages__item--time {
    &::before {
        background-image: url("../img/icon-time.svg");
    }    
}

.advantages__item--colors {
    &::before {
        background-image: url("../img/icon-colors.svg");
    }   
}

.advantages__item--delivery {
    &::before {
        background-image: url("../img/icon-delivery.svg");
    }    
}

.advantages__item--guarantee {
    &::before {
        background-image: url("../img/icon-guarantee.svg");
    }
}

@media (min-width: 576px) {
    .advantages {
        padding-top: 48px;
    }

    .advantages__title {
        font-size: 36px;
        margin-bottom: 50px;
    }

    .advantages__list {
        gap: 28px;
    }

    .advantages__item {
        font-size: 16px;
        padding: 114px 30px 30px 30px;

        span {
            display: flex;
            height: 44px;
            align-items: center;
            overflow: hidden;
        }

        &::before {
            top: 27px;
            left: 32px;
        }

    }
}    

@media (min-width: $tablet-width) {
    .advantages {
        padding-top: 60px;
    }

    .advantages__title {
        margin-bottom: 60px;
        text-align: left;
    }

    .advantages__item {
        padding: 40px 30px 40px 124px;
    }
}    

@media (min-width: 992px) {
    .advantages {
        padding-top: 92px;
    }

    .advantages__wrap {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: -8px;
            right: -126px;
            width: 288px;
            height: 566px;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("../img/bg-adv.svg");
        }
    }

    .advantages__title {
        font-size: 40px;
        margin-bottom: 70px;
    }

    .advantages__list {
        width: 770px;
    }
}    

@media (min-width: $desktop-width) {

    .advantages {
        position: relative;
        background: none;
        padding-top: 120px;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            width: 100vw;
            height: 500px;
            background-repeat: no-repeat;
            background-size: cover;
            background-image: url("../img/bg-form-desktop.svg");
        }
    }

    .advantages__wrap {
        &::before {
            right: -83px;
            top: -130px;
        }
    }

    .advantages__title {
        margin-bottom: 88px;
    }

    .advantages__list {
        grid-template-columns: repeat(3, 1fr);
        width: 995px;
        gap: 40px;
    }

    .advantages__item {
        padding: 30px 30px 30px 124px;
    }

    .advantages__item span {
        height: 64px;
    }    

}    

@media (min-width: 1350px) {
    .advantages__wrap {
        &::before {
            right: 22px;
        }
    }
}    
