
.button {
    position: relative;
    display: inline-block;
    text-align: center;
    background-color: #D32A34;    
    border: 0;
    border-radius: 12px;
    padding: 25px 20px;
    font-family: "TTFirsNeue-medium";
    font-size: 15px;
    line-height: 15px;
    overflow: hidden;
    z-index: 1;
    color: #fff;
    transition: all .3s linear;

    &:hover {
        background-color: #BB1C26;
        transition: all .3s linear;
    }

    &:active {
        background-color: #BB1C26;
        transition: all .3s linear;
    }
}

@media (min-width: 576px) {
    .button {
        padding: 27px 20px;
    }
}    
