.answers {
    padding-top: 22px;
    padding-bottom: 10px;
}

.answers__form {
    position: relative;
}

.answers__title {
    padding: 0;
    margin: 0;
    margin-bottom: 40px;
    font-size: 30px;
    color: #000000;
    font-family: "TTFirsNeue-Bold";
    line-height: 1.4;
    letter-spacing: 0.02em;
    text-align: center;
}

.answers__info {
    border-radius: 18px;
    margin-bottom: 11px;
}

.answers__section {
    margin-bottom: 30px;
}

.answers__accordeon-container {
    position: relative;
    margin-bottom: 10px;
}

.answers__block-title {
    font-family: "TTFirsNeue-demibold";
    font-size: 16px;
    line-height: 1.4;
    color: #000000;
    margin-top: 0;
    margin-bottom: 0;
    padding: 20px 60px 20px 20px;
}

.answers__btn {
    position: relative;
    display: block;
    background-color: transparent;
    border: 0;
    outline: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    border: 1px solid #efefef;
    border-radius: 18px;
    letter-spacing: 0.02em;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        right: 20px;
        width: 24px;
        height: 24px;
        transform: translateY(-50%);
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("../img/icon-plus.svg");   
        transition: .2s ease-in;    
    }

    &:hover {
        border: 1px solid #C79B67;
    }

    &:active {
        border: 1px solid #C79B67;        
    }
}

.answers__accordeon-container--opened .answers__btn {
    border: 1px solid #C79B67;
}

.answers__text {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    will-change: max-height;
    padding-right: 16px;
    font-size: 14px;
    transition: all .5s ease-out;
}

.answers__accordeon-container--opened .answers__text {
    opacity: 1;
    padding: 20px;
    font-size: 15px;
    border-radius: 16px;
    background-color: #F9F8F7;
    box-sizing: content-box;
    margin-bottom: 20px;
}

.answers__accordeon-container--opened {
    .answers__block-title { 
        color: #BB1C26;
    }    

    .answers__btn {
        &::before {
            top: 35%;
            transform: rotate(45deg);
            transition: .2s ease-in;
        }
    }
}

.answers__form-title {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    font-size: 24px;
    font-family: "TTFirsNeue-Bold";
    color: #000000;
    text-align: center;
}

.answers__form {
    margin-bottom: 30px;
}

.answers__form .form__title {
    margin-bottom: 20px;
}

.answers__form .socials .socials__item {
    width: 54px;
    height: 54px;
    margin-right: 30px;

    &:last-child {
        margin-right: 0;
    }
}

.answers .form__text-agree {
    display: block;
}


@media (min-width: 576px) {
    .answers {
        padding-top: 28px;
        padding-bottom: 30px;
    }

    .answers__section {
        margin-bottom: 50px;
    }

    .answers__title {
        font-size: 36px;
        margin-bottom: 50px;
    }

    .answers__form {
        padding-top: 27px;
    }

    .answers__form-title {
        margin-bottom: 30px;
        padding-left: 80px;
        padding-right: 80px;
    }

    .answers__block-title {
        padding: 25px 60px 25px 25px;
        font-size: 17px;
    }

    .answers__info {
        margin-bottom: 7px;
    }

    .answers__accordeon-container--opened .answers__text {
        padding: 25px 30px;
        font-size: 16px;
    }    

    .answers__form .form__title {
        margin-bottom: 28px;
    }
}    

@media (min-width: $tablet-width) {    
    .answers {
        padding-top: 42px;
        padding-bottom: 42px;
    }

    .answers__title {
        text-align: left;
        margin-bottom: 59px;
    }

    .answers__info {
        margin-bottom: 18px;
    }

    .answers .form__main {
        grid-template-columns: repeat(2, 1fr);
    }

    .answers__accordeon-container--opened .answers__text {
        padding: 30px 30px;
    }

    .answers__btn::before {
        right: 33px;
    }   

    .answers__form .form__title {
        padding-left: 40px;
    }

    .answers__form .form__socials.socials {
        margin-bottom: 28px;
        justify-content: flex-start;
    }

}    
 
@media (min-width: $tablet-width) {    
    .answers__form {
        padding-top: 30px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        column-gap: 30px;
    }

    .answers .form__main {
        grid-column: 1/3;
        order: 2;
    }

    .answers__socails {
        order: 1;
        margin-bottom: 30px;
    }

    .answers__form-title {
        padding-right: 0;
        padding-left: 220px;
        text-align: left;
        font-size: 24px;
    }

    .answers__socials.socials {
        justify-content: flex-start;
    }

    .answers .form__socials {
        margin-top: 0;
        padding-left: 20px;
    }

}    

@media (min-width: 992px) {
    .answers {
        padding-top: 60px;
        padding-bottom: 60px;
    }

    .answers__title {
        font-size: 40px;        
        margin-bottom: 70px;
    }

    .answers__block-title {
        padding-left: 30px;
    }

    .answers .form__title {
        text-align: left;
        padding-left: 0;
        padding-right: 0;
        max-width: 250px;
        margin-left: auto;
        margin-right: 30px;
    }

    .answers__section {
        margin-bottom: 54px;
    }
}    

@media (min-width: $desktop-width) {
    .answers {
        padding-top: 70px;
        padding-bottom: 70px;
    }

    .answers__wrap {
        display: grid;
        grid-template-columns: 1fr 420px;
        gap: 40px;
        align-items: start;
    }

    .answers__title {
        grid-column: 1/3;     
        margin-bottom: 51px;   
    }

    .answers__form-wrap {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }

    .answers__form {
        display: block;
        padding-top: 38px;
        padding-bottom: 30px;
    }

    .answers .form__title {
        text-align: center;
        font-size: 24px;
    }

    .answers__block-title {
        padding: 27px 60px 27px 40px;
        font-size: 17px;
    }

    .answers .form__text-agree {
        text-align: center;
        font-size: 14px;
        margin-bottom: 30px;
    }

    .answers__form .form__socials.socials {
        margin-top: 48px;
        justify-content: center;
        padding-left: 0;
    }

    .answers__info {
        margin-bottom: 23px;
    }

    .answers__accordeon-container--opened .answers__text {
        padding: 35px 40px;
    }

    .answers .form__main {
        display: block;
    }

    .answers__form .socials {
        margin-top: 20px;
    }

    .answers .form__text {
        margin-bottom: 30px;
    }

    .answers__btn::before {
        right: 40px;
    }

    .answers .callback__close-btn {
        right: -13px;
        top: -13px;
        transform: none;
    }    
}    
 