.product {
    padding-top: 30px;
    padding-bottom: 30px;
}

.product__slide {
    position: relative;
    width: 100%;
    height: 260px;
    margin-bottom: 20px;
}

.product__image {
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: right center;
    position: absolute;
    top: 0;
    left: 0;
}

.product__slide .product__image {
    border-radius: 20px;
}

.product__slide-mini .product__image {
    border-radius: 18px;
}

.product__slide-mini {   
    outline: 1px solid red;
    padding-left: 5px;
    padding-right: 5px;
    margin-bottom: 40px;
}

.product__wrap-mini {
    position: relative;
    width: 120px;
    height: 80px;
}

.product__description {
    margin-bottom: 30px;
}

.product__title {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    color: #000000;
    font-size: 24px;
    font-family: "TTFirsNeue-Bold";
}

.product__features {
    margin-bottom: 30px;
}

.product__feature {
    display: flex;
    justify-content: space-between;
    position: relative;
    margin-bottom: 5px;
}

.product__feature::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 100%;
    height: 1px;
    background-color: #eee;
}

.product__feature span {
    background-color: #fff;
    z-index: 10;
    padding-right: 10px;
    font-size: 15px;
}

.product__feature span:nth-child(2n) {
    padding-left: 10px;
    padding-right: 0;
    font-family: "TTFirsNeue-medium";
    color: #000000;
}

.product__text span {
    display: block;
    font-size: 16px;
    color: #000000;
    font-family: "TTFirsNeue-medium";
}

.product__text {
    color: #C79B67;
    font-size: 30px;
    font-family: "TTFirsNeue-Bold";
    margin-bottom: 20px;
}

.product__send {
    width: 100%;
}

.product__list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.product__item {
    position: relative;
    padding: 22px 20px 22px 88px;
    border: 1px solid #efefef;
    border-radius: 16px;
    color: #000000;
    font-family: "TTFirsNeue-medium";
    margin-bottom: 20px;

    &::before {
        content: "";
        position: absolute;
        top: 20px;
        left: 20px;
        width: 45px;
        height: 45px;
        background-repeat: no-repeat;
        background-size: contain;
    }
}

.product__item--date {

    &::before {
        background-image: url("../img/icon-date.svg");
    }
   
}

.product__item--mounting {

    &::before {
        background-image: url("../img/icon-mounting.svg");
    }
   
}

@media (min-width: 576px) {
    .product {
        padding-top: 40px;
        padding-bottom: 40px;
    }

    .product__slide {
        height: 380px;
    }

    .product__wrap-mini {
        width: 140px;
    }

    .product__slide .product__image {
        border-radius: 24px;
    }

    .product__description {
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
    }

    .product__features {
        grid-column: 1/3;
    }

    .product__text {
        margin-bottom: 0;
    }

    .product__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
    }

    .product__item {
        padding: 98px 30px 30px 30px;

        &::before {
            top: 30px;
            left: 30px;
        }
    }
}    

@media (min-width: $tablet-width) {
    .product__slide .product__image {
        border-radius: 30px;
    }

    .product__item {
        padding: 32px 30px 32px 108px;

        &::before {
            top: 30px;
            left: 30px;
        }
    }
}    

@media (min-width: 992px) {

    .product {
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 30px;
        align-items: flex-start;
    }

    .product__sliders {
        display: block;
        width: calc(100% - 400px);
    }

    .product__slide {
        height: 340px;
    }

    .product__description {
        width: 370px;
        display: block;
    }

    .product__list {
        width: 100%;
    }

}    

@media (min-width: $desktop-width) {
    .product {
        position: relative;
    }

    .product__slide {
        height: 460px;
    }

    .product__sliders {
        width: 650px;
    }

    .product__slide-mini {
        margin-bottom: 0;
    }

    .product__description {
        width: 630px;
    }

    .product__list {
        position: absolute;
        right: 0;
        bottom: 40px;
        width: 650px;
    }

    .product__item {
        margin-bottom: 0;
    }

}    



