.company {
    padding-top: 36px;
    padding-bottom: 15px;
}

.company__title {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    font-size: 30px;
    color: #000000;
    font-family: "TTFirsNeue-Bold";
    letter-spacing: 0.02em;
    line-height: 1.4;
}

.company__text-wrap {
    position: relative;
    padding-bottom: 30px;
}

.company__lead {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    font-size: 18px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";
}

.company__text {
    padding: 0;
    margin: 0;
    font-size: 15px;
}

.company__person-add {
    position: relative;
    padding: 15px 15px 23px 49px;
    margin-left: 10px;
    margin-bottom: 10px;
    font-size: 12px;
    width: 220px;
    line-height: 1.4;
    font-family: "TTFirsNeue-medium";
    background-repeat: no-repeat;
    background-image: url("../img/icon-blockquote.svg");

    &::before {
        content: "";
        position: absolute;
        top: 25px;
        left: 15px;
        width: 24px;
        height: 24px;
        background-size: contain;
        background-repeat: no-repeat;
        background-image: url("../img/icon-notif.svg");
    }
}

.company__person--main {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    align-items: center;
}

.company__person--main .company__about-person {
    padding-top: 5px;
    width: 100%;
}

.company__director {
    margin-bottom: 20px;
}

.company__person {
    margin-bottom: 30px;
}

.company__about-person {
    padding-top: 5px;
}

.company__img {
    margin-bottom: 10px;
}

.company__person-name {
    display: block;
    margin-bottom: 5px;
    font-size: 18px;
    color: #000000;
    font-style: normal;
    font-family: "TTFirsNeue-demibold";
}

.company__post {
    padding: 0;
    margin: 0;
    margin-bottom: 13px;
    font-size: 14px;
    color: #C79B67;
}

.company__person-text {
    font-size: 15px;
    padding-right: 3px;
}

@media (min-width: 576px) {
    .company {
        padding-top: 50px;
        padding-bottom: 25px;
    }

    .company__title {
        font-size: 36px;
        margin-bottom: 30px;
    }

    .company__lead {
        font-size: 20px;
        margin-bottom: 15px;
    }

    .company__text {
        font-size: 16px;
        margin-bottom: 40px;
    }

    .company__img {
        margin-top: 5px;
    }

    .company__team {
        padding-top: 30px;
    }

    .company__director {
        position: relative;
        padding-top: 45px;
    }

    .company__person-add {
        position: absolute;
        display: flex;
        align-items: center;
        top: -60px;
        left: 0;
        padding: 1px 22px 20px 74px;
        margin-left: 0;
        width: 100%;
        background-image: url("../img/icon-blockquote-2.svg");
        background-size: contain;
        background-repeat: no-repeat;
        width: 100%;
        z-index: 10;        

        &::before {
            top: 15px;
            left: 21px;
            width: 34px;
            height: 34px;
            background-image: url("../img/icon-notif-big.svg");
        }

        span {
            width: 100%;
            background-color: #ffffff;
            padding: 12px 0 12px;
            font-size: 14px;
            line-height: 1.4;
            max-width: 410px;
        }
    }    

    .company__person {
        display: grid;
        grid-template-columns: 100px 1fr;
        column-gap: 30px;
        margin-bottom: 37px;
        align-items: start;
    }
}    


@media (min-width: $tablet-width) {
    .company {
        padding-top: 18px;
        padding-bottom: 35px;
    }

    .company__title {
        font-size: 36px;
    }

    .company__director {
        padding-top: 53px;
    }

    .company__person-add {
        top: -50px;
        width: 100%;

        span {
            padding: 25px 0;
            max-width: 515px;
            margin-top: -1px;
            border-top: 1px solid #efefef;
            border-bottom: 1px solid #efefef;
            border-right: 1px solid #efefef;
            border-radius: 20px;
        }
    }    
}    

@media (min-width: 992px) {
    .company {
        padding-top: 70px;
    }

    .company__title {
        font-size: 40px;
    }

    .company__text-wrap {
        padding-bottom: 13px;
    }

    .company__team {
        padding-top: 0;
    }

    .company__director {
        padding-top: 0;        

        .company__about-person {
            margin-bottom: 15px;
            padding-left: 10px;
            padding-right: 20px;
        }
    }

    .company__person.company__person--main {
        display: grid;
        grid-template-columns: 250px 1fr;        
        margin-bottom: 60px;
     }

    .company__person {
       display: block;
       margin-bottom: 12px;
    }

    .company__person-text {
        font-size: 16px;
    }

    .company__person-add {
       top: 4px;
       left: auto;
       right: 0;
       width: calc(100% - 290px);
       background-image: url("../img/icon-blockquote-3.svg");

       span {
        padding-top: 24px;
        padding-bottom: 21px;
        border-top: none;
        border-bottom: none;
        border-right: none;
        margin-top: 0;
       }
    }   

    .company__img {
        width: 140px;
        height: 140px;
        margin-bottom: 12px;
    }
    
    .company__person--main .company__img {
        width: 250px;
        height: 250px;
        margin-bottom: 0;
    }

    .company__person--main {
        grid-template-columns: 250px 1fr;
        align-items: end;
    }

    .company__persons {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }

    .company__post {
        margin-bottom: 18px;
    }
}    

@media (min-width: $desktop-width) {
    .company {
        padding-top: 10px;
        padding-bottom: 110px;
    }

    .company__title {
        margin-bottom: 52px;
    }

    .company__add {        
        width: 280px;
        bottom: 50%;        
        transform: translateY(50%);
        right: 0;
    }   

    .company__lead {
        padding-right: 320px;
    }

    .company__text {
        max-width: 780px;
    }

    .company__team {
        display: grid;
        grid-template-columns: 1fr 380px;
        column-gap: 15px;        
    }

    .company__persons {
        gap: 0;
    }


    .company__director { 
        order: 1;
    }   

    .company__director .company__about-person {
        padding-left: 0;
        padding-right: 0;
    }   

    .company__person.company__person--main {
        display: block;
        padding-right: 0;
        margin-bottom: 12px;
    }

    .company__person-add {       
        top: -140px;
        width: 380px;
        background-image: url("../img/icon-blockquote-4.svg");

        &::before {
            top: 25px;
        }

        span {
            padding-top: 22px;
        }

    }

    .company__person--main .company__img {
        width: 380px;
        height: 527px;
        margin-bottom: 28px;
    }

    .company__img {        
        margin-bottom: 17px;
    }

    .company__person {
        display: block;
        padding-right: 63px;
        margin-bottom: 56px;
    }    

    .company__post {
        margin-bottom: 20px;
    }

    .company__person-text {
        padding-right: 4px;
    }


}    