@media (max-width: $mobile-width-only) {
    .header {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        z-index: 2000;
    }

    .main {
        padding-top: 90px;
    }
}    

@media (max-width: $mobile-width-only) and (min-width: $tablet-width) {
    .main {
        padding-top: 96px;
    }
}  

@media (max-width: 992px) and (min-width: 992px) {    
    .header {
        position: static;
    }  

    .main {
        padding-top: 0;
    }
}    