.callback {
  opacity: 0;
  height: 0;
  font-size: 0;
  right: 0;
  background-color: #ffffff;
  transition: none;
}

.callback-opened .callback {
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-left: 40px;
  padding-right: 40px;
  opacity: 1;
  height: 100%;
  font-size: 24px;
  z-index: 20;
  border: 1px solid #EFEFEF;
  border-radius: 24px;
  text-align: center;
  transition: all 0.2s;
}
.callback-opened .callback p {
  margin: 0;
  padding: 0;
  font-size: 16px;
}
.callback-opened .callback .callback-title {
  color: #000000;
  font-family: "TTFirsNeue-Bold";
  font-size: 24px;
  margin-bottom: 15px;
}

.callback__close-btn {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  right: -13px;
  top: -13px;
  width: 36px;
  height: 36px;
  background-color: #C79B67;
  border-radius: 50%;
  border: none;
  box-shadow: 0px 16px 15px -12px rgba(130, 104, 74, 0.4);
}

.callback__close-btn .callback__btn-inner {
  width: 13px;
  height: 14px;
}

.callback__close-btn .callback__btn-inner::before {
  content: "";
  display: block;
  width: 13px;
  height: 2px;
  margin-bottom: 4px;
  background-color: #ffffff;
  transform: translateY(6px) rotateZ(135deg);
}

.callback__close-btn .callback__btn-inner::after {
  content: "";
  display: block;
  width: 13px;
  height: 2px;
  margin-bottom: 0;
  background-color: #ffffff;
  transform: translateY(-6px) rotateZ(-135deg);
}

.callback__btn-bar {
  content: "";
  display: block;
  width: 18px;
  height: 2px;
  opacity: 0;
  margin-bottom: 4px;
}

@media (min-width: 768px) {
  .callback-opened .callback {
    border-radius: 30px;
  }
}
@media (min-width: 1280px) {
  .callback__close-btn {
    right: 40px;
    top: 50%;
    transform: translateY(-50%);
  }
}
