.materials {
    padding-top: 10px;
    padding-bottom: 43px;
}

.materials__title {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    font-size: 30px;
    color: #000000;
    font-family: "TTFirsNeue-Bold";
    line-height: 1.4;
    letter-spacing: 0.02em;
    text-align: center;
}

.materials__list {
    padding: 0;
    margin: 0;
    margin-bottom: 15px;
    list-style: none;
}

.materials__wrap {
    position: relative;
    margin-bottom: 25px;
    padding-top: 10px;
    width: 120px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        width: 113px;
        height: 113px;
        border-radius: 50%;
        transform: translate(-50%, -50%);
        background-color: #FAF4ED;
        z-index: 1;
    }
}

.materials__image {
    position: relative;
    z-index: 2;
}

.materials__lead {
    padding: 0;
    margin: 0;
    margin-bottom: 8px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";
    font-size: 18px;
}

.materials__text {
    padding: 0;
    padding-bottom: 28px;
    margin: 0;
    font-size: 15px;
}

@media (min-width: 576px) {
    .materials {
        padding-top: 28px;
        padding-bottom: 58px;
    }

    .materials__title {
        font-size: 36px;
        margin-bottom: 50px;
    }

    .materials__list {
        margin-bottom: 28px;
    }

    .materials__item {
        margin-bottom: 20px;
    }

    .materials .form__text-agree {
        display: none;
    }

    .materials .form__send {
        grid-column: 1/3;
    }

    .materials__wrap {
        margin-bottom: 19px;
    }

    .materials__text {
        padding: 0 0 23px;
    }
}    

@media (min-width: $tablet-width) {  
    .materials {
        padding-top: 47px;
        padding-bottom: 70px;
    }  

    .materials__title {
        text-align: left;
    }

    .materials__wrap {
        padding-top: 0;
        width: 150px;
        height: 150px;

        &::before {
            width: 141px;
            height: 141px;
        }
    }

    .materials__image--first {
        width: 120px;
        height: 113px;
    }

    .materials__image--second {
        width: 62px;
        height: 118px;
    }

    .materials__image--third {
        width: 120px;
        height: 113px;
    }

    .materials__image--forth {
        width: 136px;
        height: 128px;
    }

    .materials__list {
        margin-bottom: 35px;
    }

    .materials__item {
        display: grid;
        grid-template-columns: 170px 1fr;
        column-gap: 10px;
        align-items: center;
        padding-right: 7px;
    }

    
    .materials__lead {
        font-size: 20px;
    }

    .materials__text {
        font-size: 16px;
    }

    .materials__form.form {
        padding-top: 35px;
    }

    .materials .form__main {
       grid-template-columns: repeat(3, 1fr);
    }

    .materials .form__send {
        grid-column: 3/4;
    }

    .materials__text-wrap {
        padding-top: 7px;
    }
}    

@media (min-width: 992px) {
    .materials {
        padding-top: 60px;
        padding-bottom: 90px;
    }

    .materials__wrap {
        width: 150px;
        height: 150px;
    }

    .materials__title {
        font-size: 40px;
        margin-bottom: 70px;
    }

    .materials__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
        margin-bottom: 50px;
    }

    .materials__item {
        display: block;
        margin-bottom: 12px;
    }

    .materials__text {
        padding-bottom: 0;
    }

    .materials__text-wrap {
        padding-top: 0;
    }
}    

@media (min-width: $desktop-width) {
    .materials {
        padding-top: 110px;
    }

    .materials__title {
        margin-bottom: 90px;
    }

    .materials__list {
        margin-bottom: 70px;
    }

    .materials__item {
        display: grid;
        grid-template-columns: 200px 1fr;
        column-gap: 16px;
        margin-bottom: 0;
        padding-right: 0;
        font-size: 16px;
        line-height: 1.5;
        font-family: "TTFirsNeue-regular";
    }

    .materials__wrap {
        width: 200px;
        height: 200px;
        margin-bottom: 15px;

        &::before {
            width: 160px;
            height: 160px;
        }
    }

    .materials__image--first {
        width: 136px;
        height: 128px;
    }

    .materials__image--second {
        width: 70px;
        height: 134px;
    }

    .materials__image--third {
        width: 136px;
        height: 128px;
    }

    .materials__image--forth {
        width: 155px;
        height: 146px;
    }    
    
    .materials__text-wrap {
        margin-bottom: 23px;
    }

    .materials__form.form {
        display: grid;
        grid-template-columns: 310px 1fr;
        padding: 40px;
        column-gap: 40px;
    }

}    