.container {
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto; 
}

@media (min-width: 576px) {
  .container {
    padding-left: 33px;
    padding-right: 33px;  
  }
}  

@media (min-width: $tablet-width) {
  .container {
    padding-left: 39px;
    padding-right: 39px;       
  }
}  

@media (min-width: 992px) {
  .container {
    padding-left: 30px;
    padding-right: 30px; 
  }
}  


@media (min-width: $desktop-width) {
  .container {    
    padding-left: 25px;
    padding-right: 25px;
    margin: 0 auto;
    max-width: 1390px;
  }
}  


