.footer {
    padding-top: 40px;
    background-color: #2F2E2C;
    color: #ffffff;
    padding-bottom: 40px;
}

.footer__wrap {
    display: flex;
    flex-direction: column-reverse;
}

.footer__logo {
    display: none;
}

.footer__company {
    order: -2;
}

.footer__callback {
    order: -1;
}

.footer__callback-link {
    display: none;
}

.footer__callback-btn {
    display: none;
}

.footer__list {
    padding: 0;
    padding-top: 20px;
    padding-bottom: 15px;
    margin: 0;
    list-style: none;
    column-count: 2;
    column-gap: 29px;
}

.footer__item {
    margin-bottom: 12px;
}

.footer__link {
    color: #ffffff;
    
    &:hover {
        color: #C79B67;
    }

    &:active {
        color: #C79B67;
    }
}

.footer__email-link {
    color: #ffffff;
    
    &:hover {
        color: #C79B67;
    }

    &:active {
        color: #C79B67;
    }
}


.footer__contacts-list {
    padding: 0;
    padding-bottom: 10px;
    margin: 0;
    list-style: none;
    border-bottom: 1px solid #3E3C38;
}

.footer__contacts-item {
    position: relative;
    padding-left: 39px;
    font-size: 14px;
    margin-bottom: 15px;

    span {
        display: block;
        color: #776D59;
        margin-bottom: 8px;
    }

    &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-size: contain;
    }
}

.footer__contacts-item--address {
    &::before {
        background-image: url("../img/icon-location.svg");
    }
}

.footer__contacts-item--time {
    &::before {
        background-image: url("../img/icon-clock.svg");
    }
}

.footer__contacts-item--email {
    &::before {
        background-image: url("../img/icon-email-bottom.svg");
    }
}

.footer__socials.socials {
    justify-content: flex-start;
    margin-bottom: 30px;
}

.footer__text p {
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    font-size: 13px;

    &:last-child {
        display: flex;
        justify-content: space-between;
    }
}

@media (min-width: 576px) {
    .footer__wrap {
        display: grid;
        grid-template-columns: 1fr 110px;        
        column-gap: 30px;    
        align-items: start;
    }

    .footer__contacts-list {
        grid-column: 1/3;
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
    }

    .footer__contacts-item--address {        
        grid-column: 1/3;
    }

    .footer__contacts-item {
        font-size: 15px;
        margin-bottom: 27px;
    }   

    .footer__contacts-item span {
        margin-bottom: 4px;
    }

    .footer__callback {
        order: 1;
    }

    .footer__list {
        padding-top: 48px;
        order: 1;
        column-gap: 0;
    }

    .footer__company {
        order: 2;
        grid-column: 1/3;
    }

    .footer__socials.socials {
        padding-top: 42px;
        padding-bottom: 22px;
        margin-bottom: 0;
    }

    .footer__socials.socials .socials__item {
        margin-bottom: 20px;
        width: 44px;
        height: 44px;

        &:nth-child(2n) {
            margin-right: 0;
        }
    }

    .footer__text p {
        text-align: center;
        font-size: 14px;

        &:last-child {
            justify-content: center;

            span {
                padding: 0 15px;
            }
        }
    }
}  

@media (min-width: $tablet-width) {
    .footer {
        padding-top: 50px;
    }

    .footer__wrap {
        grid-template-columns: 1fr 1fr;
        align-items: stretch;
        column-gap: 0;
    }

    .footer__company {
        grid-column: 1/2;
        order: -1;
        border-bottom: 1px solid #3e3c38;
    }

    .footer__logo {
        display: block;
        margin-bottom: 35px;
    }

    .footer__contacts-item {
        margin-bottom: 18px;
    }

    .footer__text p {
        text-align: left;

        &:last-child {
            display: block;            
        }
    }

    .footer__text p:last-child span {
        display: block;
        padding: 0;
        margin-bottom: 6px;
    }

    .footer__contacts-list {
        display: block;
        grid-column: 2/3;
        order: -1;
        padding-left: 17px;
        padding-bottom: 30px;
    }

    .footer__callback {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .footer__list {
        padding-top: 40px;
    }

    .footer__socials.socials  {
        padding-top: 50px;
        margin-bottom: 0;
        justify-content: flex-end;
    }

    .footer__socials.socials .socials__item {
        margin-bottom: 20px;
        width: 44px;
        height: 44px;

        &:nth-child(2n) {
            margin-right: 20px;
        }

        &:last-child {
            margin-right: 0;
        }
    }
}    

@media (min-width: 992px) {
    .footer {
        padding-top: 40px;
    }

    .footer__wrap {
        grid-template-columns: repeat(3, auto);
    }

    .footer__company {
        p {
            max-width: 290px;
        }        
    }

    .footer__contacts-list {
        padding-top: 20px;
        order: 1;
        grid-column: 3/4;
        max-width: 350px;
    }

    .footer__list {
        padding-top: 20px;
        border-bottom: 1px solid #3e3c38;
        padding-right: 30px;
        column-count: 1;
    }

    .footer__callback {
        grid-column: 1/4;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    .footer__callback-btn {
        position: relative;
        display: block;
        font-size: 15px;
        padding: 20px 20px 20px 56px;
        color: #ffffff;

        &::before {
            position: absolute;
            top: 50%;
            left: 20px;
            transform: translateY(-50%);
            content: "";
            display: block;
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url(../img/icon-tel.svg);
        }        
    }
    
    .footer__callback-link {
        display: block;
        color: #ffffff;
        font-family: "TTFirsNeue-demibold";
        font-size: 22px;

        &:hover {
            color: #C79B67;
        }
    
        &:active {
            color: #C79B67;
        }
    }
}    

@media (min-width: $desktop-width) {
    .footer {
        padding-top: 70px;
        padding-bottom: 60px;
    }

    .footer__wrap {
        grid-template-columns: repeat(4, auto);
        align-items: stretch;
    }

    .footer__company {
        margin-right: 50px;
        border-bottom: none;
    }

    .footer__callback {
        grid-column: 4/5;
        flex-direction: column;
        align-items: flex-end;
        padding-top: 20px;
        padding-bottom: 10px;
    }

    .footer__callback-btn {
        padding: 25px 25px 25px 59px;
    }    

    .footer__list {
        padding-left: 20px;
        padding-bottom: 0;
        border-bottom: none;
    }

    .footer__contacts-list {
        padding-left: 50px;
        padding-bottom: 0;
        max-width: 405px;
        border-bottom: none;
    }

    .footer__contacts-item {
        padding-left: 51px;

        &::before {
            left: 17px;
        }
    }    

    .footer__socials.socials {
        padding-top: 10px;
        padding-bottom: 0;
    }

    .footer__socials.socials .socials__item {
        margin-bottom: 10px;
    }

    .footer__text p:last-child {
        display: block;

        span {
            display: block;
        }
    }
}    