.cards {
    padding-top: 40px;
    padding-left: 10px;    
    padding-bottom: 40px;    
    background: linear-gradient(180deg, #F9F8F7 20.9%, rgba(255, 255, 255, 0) 108%);
}

.cards__title {
  padding: 0;
  margin: 0;
  margin-bottom: 37px;
  font-size: 30px;
  color: #000000;
  font-family: "TTFirsNeue-Bold";
  line-height: 1.4;
  letter-spacing: 0.02em;
  text-align: center;
}

.cards__slide {
  width: 310px;
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 45px;
}

.cards__link {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: 10px;
    border: 1px solid #efefef;
    background-color: #ffffff;
    border-radius: 5px;
    color: #000000;
    border-radius: 12px;
    padding: 16px;
    font-family: "TTFirsNeue-medium";
    font-size: 14px;
    line-height: 14px;
    overflow: hidden;
    width: calc(100% - 30px);
    transition: all .3s linear;

    span {
      padding-right: 7px;
    }

    &:hover {      
        color: #BB1C26;
        transition: all .3s linear;
        border: 1px solid #D5D6DA;
    }

    &:active {
      color: #BB1C26;
      transition: all .3s linear;
      border: 1px solid #D5D6DA;
    }
}


@media (min-width: 576px) {
    .cards {
      padding-top: 50px;
      padding-bottom: 50px;
      padding-left: 23px;
    }   

    .cards__title {
      font-size: 36px;
      margin-bottom: 50px;
      margin-left: -23px;
    }

    .cards__link {
      width: 240px;
      margin: 0 auto;
      transform: translateX(-12px);
    }

}    

@media (min-width: $tablet-width) {
    .cards {
      padding-top: 62px;
      padding-left: 29px;    
      padding-bottom: 25px;  
    }

    .cards__wrap {
      position: relative;
    }

    .cards__title {
      margin-bottom: 60px;
      padding-left: 39px;
      text-align: left;
    }    

    .cards__link {
      position: absolute;
      top: -5px;
      right: 30px;
      width: 160px;
    }
}  

@media (min-width: 992px) {
    .cards {
      padding-top: 90px;
      padding-left: 0;
      padding-bottom: 30px;
    }

    .cards__wrap {
      padding-left: 30px;
      padding-right: 30px; 
      margin: 0 auto;
      max-width: 1280px;      
    }

    .cards__title {
      font-size: 40px;
      margin-bottom: 70px;
      margin-left: 0;
      padding-left: 0;
    }

    .cards__slide {
      padding-left: 15px;
      padding-right: 15px;
    }

    .cards__slider {
      margin: 0 -15px;
    }

    .cards__link {
      top: 0px;
      right: 18px;
  }

  .cards .slider .slick-dots {
    left: 0;
    bottom: -33px;
    padding-left: 0;
    text-align: center;

    li {
      background-color: #D5D6DA;
    }
  }

}  

@media (min-width: $desktop-width) {    
  .cards {
    padding-top: 40px;
    position: relative;
    background: none;

    &::before {
      //content: "";
      position: absolute;
      bottom: 0;
      right: -150px;
      left: -150px;
      height: 500px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      background-image: url("../img/bg-form2-desktop.png");
    }
  }  

  .cards__wrap {
      position: relative;
      padding-left: 25px;
      padding-right: 25px; 
      max-width: 1390px;
      margin: 0 auto;
      background: none;   
      
      &::before {
        content: "";
        position: absolute;
        right: 30px;
        top: 260px;
        height: 500px;
        width: calc(1340px + ((100vw - 1340px)/2));
        background-repeat: no-repeat;
        background-size: cover;
        background-position: right center;
        background-image: url("../img/bg-form2-desktop-left.svg");
        z-index: -1;
      }

      &::after {
        content: "";
        position: absolute;
        left: 30px;
        top: 260px;
        height: 500px;
        width: calc(1340px + ((100vw - 1340px)/2));
        background-repeat: no-repeat;
        background-size: cover;
        background-position: left center;
        background-image: url("../img/bg-form2-desktop-right.svg");
        z-index: -1;
      }
  }


  .cards__title {
    margin-bottom: 90px;
  }

  .cards__slider {
    margin: 0 -20px;
  }

  .cards__slide {
    padding-left: 20px;
    padding-right: 20px;
  }

  .cards__link {
    top: -3px;
    right: 10px;
  }

  .cards .card__link {
    bottom: 33px;
  }

  .cards .slider .slick-next, .cards .slider .slick-prev {
    top: 53%;
  }  
}  

@media (min-width: 1600px) { 
  .cards .slider .slick-next, .cards .slider .slick-prev {
    top: 53%;
  }  
}  

@media (min-width: 2500px) { 
  .cards__wrap::before {    
    top: 270px;
  }  

  .cards__wrap::after {
    top: 270px;
  } 
 
}  



