.form {
    padding: 20px;
    border-radius: 24px;
    border: 10px solid #EFEFEF;
}

.form-callback {
    position: relative;
}

.form__title {
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    font-size: 24px;
    font-family: "TTFirsNeue-Bold";
    line-height: 1.4;
    color: #000000;
    text-align: center;
}

.form__text-add {
    display: block;
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 1.4;
    letter-spacing: 0.02em;
    text-align: center;
}


.form__text {
    padding-left: 34px;
    width: 100%;
    height: 68px;
    line-height: 66px;
    flex-grow: 1;
    border-radius: 12px;
    border: 1px solid #EFEFEF;
    color: #969696;
    margin-bottom: 20px;
    background-color: transparent;
    outline: none;

    &:hover {
        color: #585858;
        border: 1px solid #D5D6DA;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.07) inset;
    }

    &:focus {
        color: #585858;
        border: 1px solid #D5D6DA;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.07) inset;
    }   
   

    &:active {
        color: #585858;
        border: 1px solid #D5D6DA;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.07) inset;
    }
}

.form__input--invalid {
    .form__text {
        outline: 1px solid #D32A34;
        color: #D32A34;
    }    
}

.form__input {
    position: relative;
}

.form__input label {
    color: #969696;
    font-size: 15px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 30px; 
    top: 20px;
    opacity: 1;
    transition: 0.3s ease all;

    sup {
        color: #D32A34;
    }
}

.form__input input:hover ~ label {
    color: #585858;
}

.form__input input:focus ~ label,
.form__input input:valid ~ label {
    opacity: 0;
}


.form__text-agree {    
    font-size: 13px;
    text-align: center;
    margin-bottom: 18px;

    span {
        color:#BB1C26;
    }
}

.form__send {
    width: 100%;
}

.form__socials.socials {
    margin-top: 20px;
}

.form.callback-opened .callback { 
    border: none;
}    

@media (min-width: 576px) {
    .form {
        padding: 30px;
    }

    .form__main {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
        align-items: center;
    }

    .form__title {
        max-width: 380px;
        margin-left: auto;
        margin-right: auto;
    }

    .form__text {
        margin-bottom: 0;
    }

    .form__text-add {
        margin-bottom: 30px;
    }

    .form__text-agree {
        text-align: left;
        margin-bottom: 0;
    }

    .form__socials.socials {
        margin-top: 30px;
    }
}   

@media (min-width: $tablet-width) {    

    .form {
        border: 15px solid #efefef;
        border-radius: 30px;
    }

    .form__text-wrap {
        position: relative;
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 50%;
            width: 18px;
            height: 53px;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("../img/icon-rect.svg");
        }
    }

    .form__title {
        font-size: 20px;
        max-width: none;
        margin-left: 0;
        margin-right: 0;
        padding-left: 15px;
        text-align: left;
        margin-bottom: 35px;
    }

    .form__text-add {
        font-size: 15px;
        text-align: left;
        margin-bottom: 35px;
        padding-left: 20px;
    }
}    

@media (min-width: 992px) {
    .form__text-agree {
        font-size: 14px;
    }

    .form__title {
        font-size: 24px;
        text-align: right;
        padding-right: 47px;
    }

    .form__text-add {
        padding-left: 30px;
        max-width: 330px;
        letter-spacing: 0;
    }
}    

@media (min-width: $desktop-width) {
    .form {
        //display: grid;
        //grid-template-columns: 310px 1fr;
        padding: 40px;
    }

    .form__main {
        grid-template-columns: repeat(4, 1fr);
    }

    .form__title {
        font-size: 20px;
        text-align: left;
    }

    .form__text-wrap {
        display: block;
        padding-right: 6px;

        .form__title {
            padding-left: 0;
            padding-right: 0;
            margin-bottom: 6px;
        }

        &::before {
            width: 0;
            height: 0;
        }
    }

    .form__text-add {
        padding-left: 0;
        margin-bottom: 0;
    }
}    