.socials {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.socials__item {
    width: 44px;
    height: 44px;
    margin-right: 20px;

    &:last-child {
        margin-right: 0;
    }
}

.socials__link {
    text-decoration: none;
    color: inherit;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    cursor: pointer;
    transition: all 0.3s linear;

    &::before {
        content: "";
        display: block;
        background-repeat: no-repeat;
        background-size: contain;
        z-index: 150;
    }
}

.socials__link--telegram {
    background-color: #0088CC;

    &::before {
        width: 22px;
        height: 18px;
        background-image: url("../img/icon-telegram.svg");
    }

    &:hover {
        background-color: #0370A6;
    }

    &:active {
        background-color: #0370A6;
    }
}

