.reviews {
    padding-top: 40px;
    padding-left: 10px;
    background: linear-gradient(180deg, #F9F8F7 20.9%, rgba(255, 255, 255, 0) 108%);
    overflow: hidden;
}

.reviews__title {
    padding: 0;
    margin: 0;
    margin-bottom: 40px;
    font-size: 30px;
    color: #000000;
    font-family: "TTFirsNeue-Bold";
    letter-spacing: 0.02em;
    line-height: 1.4;
    text-align: center;
}


.reviews__slide {
    padding-left: 10px;
    padding-right: 10px;    
    width: 310px;
    height: 380px;
    position: relative;    
    margin-bottom: 40px;    

    &:hover {
        box-shadow: 0px 30px 30px -12px rgba(0, 0, 0, 0.05);
    }

    &:hover {
        box-shadow: 0px 30px 30px -12px rgba(0, 0, 0, 0.05);
    }
}

.reviews video {    
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-color: transparent;
    border-radius: 18px;
}

.reviews .video-controls {
    position: absolute;
    top: 52%;
    left: 14%;
    z-index: 2;
}

.reviews .play-pause {
    position: relative;
    top: 0;
    left: 0;
    width: 64px;
    height: 64px;
    background-color: rgba(187, 28, 38, 0.6);
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../img/icon-play.svg");
    border: 0;
    border-radius: 50%;
    outline-offset: 5px;
    outline: 1px solid rgba(255,57,53,.2);
    font-size: 0;
    z-index: 1000;
    transition: .5s ease-in;
    cursor: pointer;

    &:hover {
        background-color: rgba(187, 28, 38, 1);
        transition: .5s ease-in;
    }

    &:active {
        background-color: rgba(187, 28, 38, 1);
        transition: .5s ease-in;
    }
}

.reviews .play-pause--hidden {
    opacity: 0;
}

.reviews__link {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: 10px;
    border: 1px solid #efefef;
    background-color: #ffffff;
    border-radius: 5px;
    color: #000000;
    border-radius: 12px;
    padding: 16px;
    font-family: "TTFirsNeue-medium";
    font-size: 14px;
    line-height: 14px;
    overflow: hidden;
    width: calc(100% - 30px);
    transition: all .3s linear;


    span {
      padding-right: 7px;
    }

    &:hover {      
        color: #BB1C26;
        transition: all .3s linear;
        border: 1px solid #D5D6DA;
    }

    &:active {
      color: #BB1C26;
      transition: all .3s linear;
      border: 1px solid #D5D6DA;
    }
}

.reviews__text {
    position: absolute;
    width: calc(100% - 20px);
    left: 10px;
    bottom: 0;
    padding-top: 20px;
    padding-bottom: 30px;
    padding-left: 30px;
    z-index: 1;
    background: linear-gradient(180deg, rgba(84, 59, 30, 0) 0%, #543B1E 100%);
    border-radius: 18px;
}

.reviews__name {
    padding: 0;
    margin: 0;
    margin-bottom: 3px;
    font-size: 18px;
    color: #ffffff;
    letter-spacing: 0.02em;
    font-family: "TTFirsNeue-Bold";
} 

.reviews__label {
    padding: 0;
    margin: 0;
    color: #ffffff;
    font-size: 15px;
}

.reviews .video-opened {
    .reviews__text {
        display: none;
    }
}

@media (min-width: 576px) {
    .reviews {
        padding-top: 50px;
        padding-left: 23px;
    }

    .reviews__title {
        font-size: 36px;
        margin-left: -23px;
        margin-bottom: 50px;
    }

    .reviews__slider {
        margin-right: -10px;
    }

    .reviews__slide {
        margin-bottom: 50px;
    }   

    .reviews__link {
        width: 240px;
        margin: 0 auto;
        transform: translateX(-12px);
    }
}    

@media (min-width: $tablet-width) {
    .reviews {
        padding-left: 30px;
        padding-top: 60px;
    }

    .reviews__title {
        padding-left: 39px;
        font-size: 36px;
        margin-bottom: 60px;
        text-align: left;
    }

    .reviews__wrapper {
        position: relative;
    }

    .reviews__slider {
        margin-right: -110px;
    }

    .reviews__slide {
        width: 310px;
    }

    .reviews__link {
        position: absolute;
        top: -5px;
        right: 30px;
        width: 160px;
    }

    .reviews__name {
        font-size: 20px;
    }
}    

@media (min-width: 992px) {
    .reviews {
        padding-top: 46px;
        padding-left: 30px;
        padding-right: 30px; 
        margin: 0 auto;
    }

    .reviews__title {
        padding-left: 0;
        margin-left: 0;
        font-size: 40px;
        margin-bottom: 70px;
    }

    .reviews__slider {
        margin: 0 -15px;
    }

    .reviews__slide {
        padding-left: 15px;
        padding-right: 15px;
    }

    .reviews__link {
        top: 2px;
        right: -10px;
    }

    .reviews .slider .slick-dots {
        left: 0;
        bottom: -25px;
        padding-left: 0;
        text-align: center;
    
        li {
          background-color: #D5D6DA;
        }
      }

    .reviews  .slider .slick-dots li.slick-active {
        background: #bb1c26;
    }

    .reviews__text {
        width: calc(100% - 30px);
        left: 15px;
    }
}    

@media (min-width: $desktop-width) {
    .reviews {
        position: relative;        
        padding-top: 75px;
        padding-bottom: 77px;
        padding-left: 0;
        padding-right: 0;
        background: none;

        &::before {
            content: "";
            position: absolute;
            top: 283px;
            right: 0;
            width: 100vw;
            height: 500px;
            background-repeat: no-repeat;
            background-size: cover;
            background-image: url("../img/bg-form3-desktop.svg");
        }
    }

    .reviews__wrapper {
        position: relative;
        max-width: 1390px;
        padding-left: 25px;
        padding-right: 25px;        
        margin: 0 auto;
    }

    .reviews__text {
        width: calc(100% - 40px);
        left: 20px;
    }

    .reviews__title {
        position: absolute;
        top: 66px;
        left: 25px;
        margin-bottom: 0;
        font-size: 40px;
        max-width: 420px;
    }

    .reviews__slider {
        background-color: transparent;
        margin: 0 -20px;
    }


    .reviews__slide {
        padding-left: 20px;
        padding-right: 20px;
        position: relative;
        margin-top: 260px;
        height: 320px;        
        transition: .1s ease-in;
        z-index: 100;
    }

    .reviews__slide.slick-current ~ .reviews__slide ~ .reviews__slide {
        position: relative;
        z-index: 100;
        height: 580px;
        margin-top: 0;
        transition: .1s ease-in;

        .video-controls {
            top: 44%;
            left: 50%;
            transform: translateX(-50%);
        }
      }

    .reviews__link {
        position: absolute;
        top: 87px;
        right: calc(100% / 3 + 10px);
        z-index: 5;
    }  

    .reviews .video-controls {
        top: 40%;
    }

    .reviews__text {
        padding-left: 40px;
    }

    .reviews .slider .slick-next, .reviews .slider .slick-prev {
        top: 67%;
    }    
}    



