.goods {
    padding-left: 10px;
}

.goods__title {
    padding: 0;
    margin: 0;
    margin-bottom: 40px;
    font-size: 30px;
    color: #000000;
    font-family: "TTFirsNeue-Bold";
    text-align: center;
    letter-spacing: 0.02em;
    line-height: 1.4;
}

.goods__slide {
    width: 310px;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 40px;
}

.goods .card__title {
    font-size: 18px;
}

@media (min-width: 576px) {
    .goods {
        padding-left: 23px;
    }

    .goods__title {
        font-size: 36px;
        margin-bottom: 50px;
        margin-left: -23px;
    }
}    

@media (min-width: $tablet-width) {
    .goods {
        padding-top: 8px;
        padding-left: 29px;      
    } 
    

    .goods__title {
        padding-left: 15px;
        margin-left: 0;
        margin-bottom: 60px;
        text-align: left;
    }  

    .goods .card__title {
        font-size: 20px;
    }
}    

@media (min-width: 992px) {
    .goods {
      padding-top: 30px;
      padding-left: 30px;
      padding-right: 30px; 
      margin: 0 auto;
      max-width: 1280px;
    }

    .goods__slide {
        padding-left: 15px;
        padding-right: 15px;
    }

    .goods__slider {
      margin: 0 -15px;
    }

    .goods__title {
        font-size: 40px;
        margin-bottom: 70px;
        padding-left: 0;
    }
}  


@media (min-width: $desktop-width) {
    .goods {
      padding-top: 80px;  
      padding-left: 25px;
      padding-right: 25px; 
      max-width: 1390px; 
    }

    .goods__title {
        margin-bottom: 90px;
    }

    .goods__slider {
        margin: 0 -20px;
    }

    .goods__slide {
        padding-left: 20px;
        padding-right: 20px;
    }

    .goods .card__title {
        margin-bottom: 13px;
    }
}  