.work {
    padding-top: 38px;
    background: linear-gradient(180deg, #F9F8F7 20.9%, rgba(255, 255, 255, 0) 108%);
}

.work__title {
    padding: 0;
    padding-left: 20px;
    margin: 0;
    margin-bottom: 40px;
    font-size: 30px;
    color: #000000;
    font-family: "TTFirsNeue-Bold";
    letter-spacing: 0.02em;
    line-height: 1.4;
    text-align: center;
}

.work__list {
    padding: 0;
    margin: 0;
    list-style: none;
    counter-reset: program;
}

.work__item {
    position: relative;
    padding: 30px 20px 26px 124px;
    background-color: #ffffff;
    border: 1px solid #efefef;
    border-radius: 16px;
    margin-bottom: 21px;
    font-family: "TTFirsNeue-medium";
    font-size: 16px;
    color: #000000;    
    line-height: 1.4;
    counter-increment: program;

    &::before {
        content: "";
        position: absolute;
        top: 20px;
        left: 30px;
        width: 64px;
        height: 64px;
        background-repeat: no-repeat;
        background-size: contain;
    }

    &::after {
        content: counter(program);
        position: absolute;
        top: 50%;
        left: -16px;
        width: 32px;
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background-color: #D32A34;
        transform: translateY(-50%);
        color: #ffffff;
    }
}

.work__item--sample {
    &::before {
        background-image: url("../img/icon-sample.svg");
    }    
}

.work__item--design {
    &::before {
        background-image: url("../img/icon-design.svg");
    }    
}

.work__item--manufacture {
    &::before {
        background-image: url("../img/icon-manufacture.svg");
    }    
}

.work__item--installation {
    &::before {
        background-image: url("../img/icon-installation.svg");
    }    
}

@media (min-width: 576px) {
    .work {
        padding-top: 50px;
        padding-bottom: 30px;
    }

    .work__title {
        font-size: 36px;
        margin-left: -23px;
        margin-bottom: 50px;
    }

    .work__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }

    .work__item {
        padding: 114px 30px 28px 30px;
        margin-bottom: 0;

        &::before {
            top: 29px;
        }

        &::after {
            top: 32%;
        }
    }
} 

@media (min-width: $tablet-width) {
    .work {
        padding-top: 57px;
    }

    .work__title {
        font-size: 36px;
        text-align: left;
        margin-left: 0;
        padding-left: 0;
        margin-bottom: 60px;
    }

    
    .work__item {
        padding: 0 30px 0 135px;
        display: flex;
        align-items: center;
        height: 124px;

        &::after {
            top: 50%;
        }
    }
}    

@media (min-width: 992px) {    
    .work {
        padding-top: 144px;
    }
    
    .work__wrap {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: -137px;
            right: -94px;
            width: 370px;
            height: 562px;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("../img/bg-work.svg");
        }
    }

    .work__title {
        font-size: 40px;
        margin-bottom: 70px;
    }

    .work__list {
        width: 770px;
    }

}    

@media (min-width: $desktop-width) {
    .work {
        position: relative;
        background: none;
        padding-top: 115px;
        padding-bottom: 20px;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            width: 100vw;
            height: 500px;
            background-repeat: no-repeat;
            background-size: cover;
            background-image: url("../img/bg-form-desktop.svg");
        }
    }

    .work__wrap {
        &::before {
            top: -122px;
            right: 35px;
        }
    }

    .work__title {
        margin-bottom: 90px;
    }

    .work__list {
        width: 880px;
        gap: 40px;
    }
}    